import React, { useState, useEffect }  from 'react';
import { FaUserPlus } from 'react-icons/fa';
import { GiTakeMyMoney } from 'react-icons/gi';
import { RiUserSearchFill } from 'react-icons/ri';
import { GiArchiveResearch } from 'react-icons/gi';
import { VscCallIncoming,VscCallOutgoing } from 'react-icons/vsc';
import { Link } from 'react-router-dom';
import {get} from './../../helpers/storage_helper';
import {format_date,lbl_amt} from './../../helpers/common_helper';
import {dd_value} from '../../actions/common_actions';
import './../../styles/flow.css';
import {Form,Modal,Col,Row} from 'react-bootstrap';
import FlowComponent from '../common/component/core/FlowComponent';
import {validateCustomer} from '../../actions/borrower_actions';
import InstantDisburseLoan from './../loan/component/InstantDisburseLoan';

import {repeatFloatAdvance,get_repayment_summary,create_capture_payment} from '../../actions/loan_actions';



class HomeLinks extends FlowComponent{

    state = {
        cust_token: false,
        modalBox :false,
        capture_payment:false,
        cust_profile : 'hide_div',
        repayment_style :'disable_div',
        loan_type:"",
        loan_data :{},
        cust_data:{},
        timer_sec :0,
        timer : '',
        timer_min:0,
        reviewed : 0,
        opacity : ""
    }

    tick() {
     
        if(this.state.timer_sec == 0 || this.state.timer_sec != 0 ){
        this.setState({
          timer_sec: this.state.timer_sec + 1,
           });
        if(this.state.timer_sec == 60){
            this.setState({
                timer_min:this.state.timer_min +1,
                timer_sec : 0
            })
        }
      }else{

        this.refresh_timer()
      }     
    }

    handle_custID_change = (event) => {
        this.setState({cust_token : event.target.value})       
    }

    validateCustomerVisit(cust_token){ 
        if(cust_token){
            return true
        }else{
            alert("Please Enter Customer ID")
            return false
        }

    }
    refresh_timer = () =>{
        setInterval(() => this.tick(),1000) 
    }

    handleIncomingCall = () =>{
        
        this.setState({modalBox:true})
        this.server_time()
        if(this.state.timer_sec == 0){
            this.refresh_timer()
        }else{
            this.setState({timer_sec:0,timer_min:0})
        }
        
        
    }
    handleClose = (event) => {
        this.setState({modalBox: false, cust_profile : 'hide_div',cust_data:{}});
        //window.location.reload();


    }
    CaptureModalClose = (event) =>{
        this.setState({capture_payment: false,opacity:""});
    }

    submit_capture_payment = (event) =>{
        const resp = window.confirm("Are you sure capture the payment?");
            if(resp == false){
                return;
            }
        const request = {loan_doc_id : this.state.selected_payment.loan_doc_id,
                         acc_stmt_txn_id : this.state.selected_payment.acc_stmt_txn_id, 
                         amount : this.state.selected_payment.amount}
        create_capture_payment (this.req(request))
        .then((response)=>{
            if(!response){return null;}
            if(response.status === "success"){
                this.setState({opacity:""})
                alert('Payment captured successfully')
                 this.handleValidateCustomer();
                 // window.location.reload();

                this.CaptureModalClose()
            }else{
                  alert(response.message)
            }
        })
    }


    capture_repayment = (event) => {
          
        this.setState({acc_stmts :{acc_stmt_txn_id:this.state.cust_data.acc_stmts.id , loan_doc_id:this.state.cust_data.ongoing_loan_doc_id }});
        const request = this.state.acc_stmts
        get_repayment_summary(this.req(request))
            .then((response) => {
                if(!response){return null;}
                if(response.status === "success"){
                  this.setState({capture_payment: true, selected_payment : response.data,opacity:0.9});
                }else{
                  alert(response.message)
                }
        })
  // loan_doc_id, acc_Stmt_id, amount

    }

    repeatFloatAdvance = (event) => {
        const resp = window.confirm("Are you sure to confirm?");
        if(resp == false){
            return;
        }
        const request = {"cust_id" : this.state.cust_data.cust_id, "loan_doc_id" : this.state.cust_data.last_loan_doc_id};
        repeatFloatAdvance(this.req(request))
            .then((response) => {
                if(!response){return };
                    if(response){ 
                        console.log(response)
                        const loan_appl_doc_id = response.data.loan.loan_appl_doc_id;   
                        const approver_name = response.data.loan.loan_approver_name;    
                        this.setState({approver_name});
                        alert(response.message+" and submitted to "+approver_name);
                        this.handleValidateCustomer();
     
                    }else if (response.data.loan){
                        const loan_doc_id = response.data.loan.loan_doc_id
                        this.setState({loan_doc_id, viewLoan: true});
                        alert(response.message);
                    }
                    else{
                        alert(response.message)
                    }
                        
        });
    }

    handleReviewChange = (event) => {
      const target = event.target;
      const value = target.checked;   
      var reviewed = this.state.reviewed;
      var repayment_style = "disable_div"
      if(value){
        reviewed++
      }else{
        reviewed--
      }
      
      if(reviewed == 6){
        repayment_style = ""
      }
      this.setState({
       reviewed, repayment_style
      });
    }
   
    handleValidateCustomer = (event) =>{
        if(event){
           this.target = event.target.id 
        }
        
        var cust_token = this.state.cust_token
        var is_valid = this.validateCustomerVisit(cust_token)
        console.log(is_valid)
        if(is_valid){
            validateCustomer(this.req({cust_token,incl_profile:true,incl_fa:true},true)).
                then((response) =>{
                    if(!response){return };
                    if(response.status === "success"){
                        console.log(response.data)
                        if(response.data){
                            if(response.data.last_loan){
                                this.setState({cust_profile:'show_div',cust_data:response.data,loan_data:response.data.last_loan,loan_type:"last_loan"})
                            }
                            else if(response.data.current_loan){
                                this.setState({cust_profile:'show_div',cust_data:response.data,loan_data:response.data.current_loan,loan_type:"current_loan"})
                                 console.log(this.state.loan_type)
                            }

                            else{
                                this.setState({cust_profile:'show_div',cust_data:response.data, loan_type:"no_loan"})
                                
                            }                           
                        }
                        if(response.data && response.data.cust_id){
                            if(this.target == 'customer_visit'){
                                window.open(`/checkin/${response.data.cust_id}`,"_blank")
                            }
                        }
                      
                   }
                   else{
                     alert(response.message  +"\n" + JSON.stringify(response.data));  
                   }
                } )
        }

    }

    RepaymentSummaryModal = (summary,loan_data) => {
    if(!summary){
      return null
    }
    return(
    <Modal show={this.state.capture_payment} onHide={this.CaptureModalClose}  size="l">
          <Modal.Header closeButton show={this.state.capture_payment} onHide={this.CaptureModalClose}>
              <Modal.Title>Capture FA Payment ({summary.loan_doc_id})</Modal.Title>
          </Modal.Header>
          <Modal.Body>        
              <div className="container">
              <p>Review the below payment data by clicking over them. </p>
              <form>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Account txn ID</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_1" className="toggle visually-hidden"  onChange={this.handleReviewChange} value="1"/>
                          <label className="select_toogle" for="toggle_1">{summary.stmt_txn_id} </label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>

                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Txn description</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_2" className="toggle visually-hidden"  onChange={this.handleReviewChange} value="1"/>
                          <label className="select_toogle" for="toggle_2">{summary.descr}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Customer</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_3" className="toggle visually-hidden"  onChange={this.handleReviewChange} value="1"/>
                          <label className="select_toogle" for="toggle_3">{summary.cust_name} | {summary.biz_name}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Cust ID</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_4" className="toggle visually-hidden" onChange={this.handleReviewChange} value="2"/>
                          <label className="select_toogle" for="toggle_4">{summary.cust_id} | {summary.data_prvdr_cust_id}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Payment amount</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_5" className="toggle visually-hidden" onChange={this.handleReviewChange} value="3"/>
                          <label className="select_toogle" for="toggle_5">{lbl_amt(loan_data.paid_amount, this.currency_code)}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Outstanding amount</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_6" className="toggle visually-hidden" onChange={this.handleReviewChange} value="3"/>
                          <label className="select_toogle" for="toggle_6">{lbl_amt(summary.current_os_amount, this.currency_code)}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>                   
                   <div className="row">
                      <label><input type="checkbox" onChange={this.handleSMSCheckboxClick} defaultChecked/>&nbsp;
                      Send payment confirmation notification SMS</label>
                    </div>
              </form>
              </div>              
             {this.state.processing && <p style={{color: "red",textAlign: "right"}}>{this.state.processing}</p>}
            </Modal.Body>

          <Modal.Footer>
            <div className="col-md-8 reset-margin reset-padding">
                <button type="button" className= {`btn btn-primary float-right mr-1 ${this.state.repayment_style}`} onClick={this.submit_capture_payment} disable>Capture Payment </button>
                <button type="button" className="btn btn-outline-primary float-right mr-1" onClick={this.CaptureModalClose} data-dismiss="modal" aria-label="Close">Cancel</button>    
            </div>
          </Modal.Footer>
    </Modal>
         
          )
  }
   

    render(){
        let {biz_name,cust_name,data_prvdr_cust_id,cust_id,cust_mobile_num,cust_dob,ongoing_loan_doc_id,last_loan_doc_id}=this.state.cust_data
        let{loan_principal,duration,flow_fee,due_amount,loan_doc_id,cust_acc_id,to_acc_num,lender_code,data_prvdr_code,currency_code,due_date,status,paid_amount,current_os_amount,overdue_days} = this.state.loan_data
        let{loan_type} = this.state

        this.instant_disburse_loan = {biz_name, cust_name, data_prvdr_cust_id, loan_principal,
                                            duration, flow_fee, due_amount, loan_doc_id, 
                                            cust_id, cust_acc_id, to_acc_num, lender_code, data_prvdr_code, currency_code,due_date }
        return(
            <>
            {this.RepaymentSummaryModal(this.state.selected_payment,this.state.loan_data)}
            <div className="home-menu box">
                <tr>
                    <td><h3> <GiTakeMyMoney className="icon" /></h3></td>
                    <td><Link to={'/fa_appl'}>Apply FA</Link></td>
                </tr> 
                <tr>
                    <td><h3> <RiUserSearchFill className="icon"/></h3></td>
                    <td><Link to={'/borrower/search'}>Search Customer</Link></td>
                </tr>
                <tr>
                    <td><h3> <GiArchiveResearch className="icon"/></h3></td>
                    <td><Link to={'/fa/search'}>Search Float Advance</Link></td>
                </tr>
                <tr>
                    <td><h3> <FaUserPlus className="icon"/></h3></td>
                    <td><Link to={'borrower/indiv/create'}>Create Customer Profile</Link></td>
                </tr>
                <tr>
                    <td></td>
                    <td><Form.Control id="cust_token" onChange={(e)=>{this.handle_custID_change(e)}}/>
                    </td>&nbsp;
                    <td>

                        <button id="customer_visit" type="button" className="btn btn-primary "  onClick={this.handleValidateCustomer} >Customer Visit</button></td>

                   

                    {/* <td><Form.Control id="inlineFormInputName" onchange={this.handletext } />
                    </td> */}
                </tr>
                {/*
                <tr >

                <td></td>
                    <td >
                        <button id="incoming_call" type="button" className="btn btn-primary mt-3 mr-3" onClick = {(e) => {this.handleIncomingCall(e)}} variant="primary" ><VscCallIncoming className ="mr-1" color="white"/>Log Incoming Call </button>
                        <button id="outgoing_call" type="button" className="btn btn-primary mt-3 " variant="success" ><VscCallOutgoing className ="mr-1" color="white"/>Log Outgoing Call</button>

                    </td>
                </tr>*/}
                <tr>                  
                </tr>
                 <Modal  show={this.state.modalBox} onHide={this.handleClose} className="model_window p-0" style={{opacity:this.state.opacity}}>
                    <Modal.Header closeButton show={this.state.modalBox} onHide={this.handleClose} >
                        <Modal.Title>Log Incoming Call <VscCallIncoming className ="mr-1"/></Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                    <Form>
                        <Form.Row className="justify-content-md-center m-5">
                            <Form.Label >Any Customer ID / Mobile No / National ID</Form.Label>
                            <Form.Group as={Col} className="col-2">
                                <Form.Control id="cust_token" onChange={(e)=>{this.handle_custID_change(e)}}/>
                            </Form.Group>
                            <Form.Group as={Col} className="col-2">
                                 <button id="search" type="button" className="btn btn-primary" onClick={this.handleValidateCustomer} >Search</button>

                            </Form.Group>
                            <Form.Group as={Col} className="col-2">
                               <h4 style ={{color:"red"}}> Counter : {this.state.timer_min}m {this.state.timer_sec}s</h4>
                            </Form.Group>
                        </Form.Row>
                    </Form>
                    <div className={this.state.cust_profile} >
                    <div className="panel_border mx-auto  my-4 p-4 ">
                        <Col lg="12">
                            <Row className="py-1">
                                <Col>Customer Name</Col>
                                <Col><b>{cust_name}</b></Col>
                                <Col>Business Name</Col>
                                <Col><b>{biz_name}</b></Col>
                            </Row>
                           <Row className="py-1">
                                <Col>Mobile Number</Col>
                                <Col><b>{cust_mobile_num}</b></Col>
                                <Col>DOB</Col>
                                <Col><b>{format_date(cust_dob)}</b></Col>
                            </Row>
                            <Row className="py-1">
                            {ongoing_loan_doc_id ?
                                <>
                                <Col>Current FA ID</Col>
                                <Col><b>{ongoing_loan_doc_id}</b></Col></>
                                :
                                <>
                                <Col>Last FA ID</Col>
                                <Col><b>{last_loan_doc_id}</b></Col></>
                                
                            }
                                <Col>Status</Col>
                                <Col><b>{dd_value(status)}</b></Col>
                                
                            </Row>
                            <Row className="py-1"> 
                                <Col>Due Amount</Col>
                                <Col><b>{lbl_amt(due_amount,this.currency_code)}</b></Col>

                                <Col>Paid Amount</Col>
                                {paid_amount?
                                <Col><b>{lbl_amt(paid_amount,this.currency_code)}</b></Col>
                                :
                                <Col><b>0</b></Col>}
                                
                            </Row>
                             <Row className="py-1">
                                <Col>Current OS Amount</Col>
                                <Col><b>{lbl_amt(current_os_amount,this.currency_code)}</b></Col>
                                <Col></Col>
                                <Col></Col>
                            </Row>
                            <Row className="py-1">
                                <Col>Due Date</Col>
                                <Col><b>{format_date(due_date)}</b></Col>
                                <Col>Overdue Days</Col>
                                <Col><b>{overdue_days}</b></Col>
                            
                            </Row>
                         </Col>
                    </div>
                    <Form>
                        <Form.Row className="justify-content-md-center">
                    {loan_type == "current_loan"  && (status == "overdue" || status == "due" || status == "ongoing" )&&

                        <button  type="button" id="capture_repayment" className="btn btn-primary" onClick ={this.capture_repayment} >Capture Payment </button>                       
                    }
                    
                    
                   
                    {loan_type == "last_loan" &&
                    <>
                        <button id="repeat_fa" type="button" className="btn btn-primary mr-5" onClick={this.repeatFloatAdvance} >Repeat Last FA</button>&nbsp;
                        <Link type="button" to={"/fa_appl_for/"+cust_id} target="_blank" className="btn btn-primary">Apply New FA</Link>
                    </>

                    }
                    {loan_type == "no_loan" && 
                        <Link type="button" to={"/fa_appl_for/"+cust_id} target="_blank" className="btn btn-primary">Apply New FA</Link>

                    }
                    {loan_type == "current_loan" && status == "pending_disbursal" &&
                        <InstantDisburseLoan id="instant_disbursal_txn" 
                            data={this.instant_disburse_loan} 
                            title="Instant Disbursal Loan" 
                            mode={this.props.mode} />   
                    }

                        </Form.Row>
                    </Form>                    
                    </div>
                    </Modal.Body>
                    <Modal.Footer>
                    </Modal.Footer>
                </Modal>
                
            </div>
        </>
           
        );
    }
}
export default HomeLinks;