import React from 'react';
import FlowContainer from '../../common/container/core/FlowContainer';
import {list_lender_txn_stmts} from '../../../actions/lender_actions';
import $ from 'jquery';
import {rt_algn,lbl_amt} from '../../../helpers/common_helper';
import {format_date_tbl} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';
import {ImArrowUpRight,ImArrowDownLeft} from "react-icons/im";
import {Button,Modal} from 'react-bootstrap';
import { Link} from 'react-router-dom';
import {get_repayment_summary} from '../../../actions/loan_actions';
import {create_capture_payment} from '../../../actions/loan_actions';

class ListTodayAccountStmtTxnContainer extends FlowContainer{

  state = {
    last_updated : null,
    updated_since : 0,
    timer :0,
    buttonName : 'Pause Refresh',
    btn_variant : 'danger',
    timerID : '',
    modalBox : true,
    modal: null
  }
 
  constructor(){
    super()
    this.REFRESH_IN = 120
  }
 componentWillUnmount() {
    
    clearInterval(this.timerID);
  }

  tick() {
      if(this.state.timer !=0){
        this.setState({
          timer: this.state.timer - 1,
          updated_since : this.REFRESH_IN - this.state.timer 
        });
      }else{
      
      this.refreshPaymentsList();
      }
    }

  loan_doc_id(txn){
      const loan_doc_id = txn.loan_doc_id
      const id = txn.id
      const txn_type = txn.stmt_txn_type
      if(loan_doc_id && txn_type == "credit"){
        return <Button variant="danger" id={id} onClick={(e) => this.capture_repayment(e,loan_doc_id)}>Capture Payment</Button>
      }
    }

  handleClose = (event) => {
      this.setState({modalBox: false,selected_payment : null});
  }

  handleSubmit = (event) =>{
      const resp = window.confirm("Are you sure capture the payment?");
      if(resp == false){
        return;
      }
    const request = {loan_doc_id : this.state.selected_payment.loan_doc_id,
                     acc_stmt_txn_id : this.state.selected_payment.acc_stmt_txn_id, 
                     amount : this.state.selected_payment.amount}
    create_capture_payment (this.req(request))
    .then((response)=>{
      
        if(!response){return null;}

        if(response.status === "success"){
            alert('Payment captured successfully')
            this.handleClose()
        }else{
              alert(response.message)
        }
    })
  }

  capture_repayment = (event, loan_doc_id) => {
    const request = {acc_stmt_txn_id : event.target.id, loan_doc_id};
    get_repayment_summary(this.req(request))
        .then((response) => {
            if(!response){return null;}
            if(response.status === "success"){
              this.setState({modalBox: true, selected_payment : response.data});
            }else{
              alert(response.message)
            }
      })
  // loan_doc_id, acc_Stmt_id, amount

  }



    componentWillMount()
    {

     this.refreshPaymentsList();

    }


    chk_acc_type(cr_amt,dr_amt){
        if (cr_amt === 0 && dr_amt === 0) {
          return null
        }
        else if (cr_amt === 0){
          return <ImArrowUpRight color="red"/>
        }
        else if (dr_amt === 0){
          return <ImArrowDownLeft color = "green"/>
        }else{
          return null
        }

    }

   
    pauseRefresh = () => {
        if(this.timerID){
            clearInterval(this.timerID);
            this.timerID = null
            this.setState({buttonName : 'Start Refresh',
                           btn_variant : 'success' })    
        }else{
            this.timerID = setInterval(() => this.tick(),1000);
            this.setState({buttonName : 'Pause Refresh',
                           btn_variant : 'danger'})
        }
        
        

    }


    refreshPaymentsList = () =>{
       
        clearInterval(this.timerID);
        var statement_search = {today : true}
        statement_search.account_id = this.props.match.params.acc_id;

        list_lender_txn_stmts(this.req({statement_search}))
            .then((response) => {
                var prev_table = $('#statement_search').DataTable();
                prev_table.destroy();
                if(!response)
                {
                    return null;
                }

                if(response.status === "success"){
                    var account_num = response.data.acc_txt
                    const lenderTxnStmts = response.data.account_txns.map((txn,i)=>
                          <tr key={i}>
                          <td>{++i}</td>
                          <td>{this.chk_acc_type(txn.cr_amt,txn.dr_amt)}</td>
                          <td > {dd_value(txn.stmt_txn_type)}</td>
                          <td > {rt_algn(lbl_amt(txn.cr_amt, this.currency_code))}</td>
                          <td > {rt_algn(lbl_amt(txn.dr_amt, this.currency_code))}</td>
                          <td > {rt_algn(lbl_amt(txn.balance, this.currency_code))}</td>
                          <td > {txn.stmt_txn_id}</td>
                          <td > {txn.descr}</td>
                          <td > {format_date_tbl(txn.stmt_txn_date,true)}</td>
                          <td >{txn.loan_doc_id ?
                                <Link to={"/fa/view/"+txn.loan_doc_id} target="_blank" >{txn.loan_doc_id}</Link>
                                  :
                                <span>NA</span>}</td>
                          <td>{this.loan_doc_id(txn)}</td> 
                          </tr>
                     );
                    this.timerID = setInterval(() => this.tick(),1000);
                    var dt = new Date()
                    dt.setMinutes( dt.getMinutes() - 2 );
                    this.setState({last_updated: (dt.toLocaleTimeString('en-US', { hour: 'numeric', minute: 'numeric',second: 'numeric', hour24: true })).toString(), 
                                    timer : this.REFRESH_IN,
                                    lenderTxnStmts,
                                    account_result:"show_div",
                                    account_num:  account_num });
                    
                        $('#statement_search').removeAttr('width').dataTable({
                            "pageLength": 20,
                            "order" : [],
                                "columns": [
                                  {"width" : "10%"},
                                  {"width" : "10%"},
                                  {"width": "10%"},
                                  {"width": "10%"},
                                  {"width": "15%"},
                                  {"width": "15%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                  {"width": "20%"},
                                   {"width": "20%"}
                                ],
                                dom:'Bfrtip',
                                buttons: [
                                'csv','excel','print'
                                ]
                        });        
                }else{
                  alert(response.message + "\n" + JSON.stringify(response.data));  
                }

  });
  }

get_message = () => {
    if(!this.timerID){
        return <h4 style={{color: "red"}}>Paused Refreshing</h4>
    }
    if(this.state.timer != 0){ 
        return <h4 style={{color: "blue"}}>Refreshing in {this.state.timer} seconds</h4>
    }else{
        return <h4 style={{color: "green"}}>Refreshing please wait.</h4>
    }
               
}

RepaymentSummaryModal = (summary) => {
    if(!summary){
      return null
    }
    return(
    <Modal show={this.state.modalBox} onHide={this.handleClose} size="l">
          <Modal.Header closeButton show={this.state.modalBox} onHide={this.handleClose}>
              <Modal.Title>Capture FA Payment ({summary.loan_doc_id})</Modal.Title>
          </Modal.Header>
          <Modal.Body>        
              <div className="container">
              <p>Review the below payment data by clicking over them. </p>
              <form>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Account txn ID</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_1" className="toggle visually-hidden"  onChange={this.handleReviewChange} value="1"/>
                          <label className="select_toogle" for="toggle_1">{summary.stmt_txn_id} </label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>

                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Txn description</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_2" className="toggle visually-hidden"  onChange={this.handleReviewChange} value="1"/>
                          <label className="select_toogle" for="toggle_2">{summary.descr}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Customer</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_3" className="toggle visually-hidden"  onChange={this.handleReviewChange} value="1"/>
                          <label className="select_toogle" for="toggle_3">{summary.cust_name} | {summary.biz_name}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Cust ID</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_4" className="toggle visually-hidden" onChange={this.handleReviewChange} value="2"/>
                          <label className="select_toogle" for="toggle_4">{summary.cust_id} | {summary.data_prvdr_cust_id}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Payment amount</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_5" className="toggle visually-hidden" onChange={this.handleReviewChange} value="3"/>
                          <label className="select_toogle" for="toggle_5">{lbl_amt(summary.amount, this.currency_code)}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>
                  <div className="rule-form-group form-group row reset-margin">
                      <label for="" className="col-sm-4 label__name reset-padding">Outstanding amount</label>
                      <div className="col-sm-8 reset-padding">
                        <div>
                          <input type="checkbox" id="toggle_6" className="toggle visually-hidden" onChange={this.handleReviewChange} value="3"/>
                          <label className="select_toogle" for="toggle_6">{lbl_amt(summary.current_os_amount, this.currency_code)}</label>
                          <div className="control-me"></div>
                        </div>
                      </div>
                  </div>                   
                   <div className="row">
                      <label><input type="checkbox" onChange={this.handleSMSCheckboxClick} defaultChecked/>&nbsp;
                      Send payment confirmation notification SMS</label>
                    </div>
              </form>
              </div>              
             {this.state.processing && <p style={{color: "red",textAlign: "right"}}>{this.state.processing}</p>}
            </Modal.Body>

          <Modal.Footer>
            <div className="col-md-8 reset-margin reset-padding">
                <button type="button" className= {`btn btn-primary float-right mr-1 ${this.state.repayment_style}`} onClick={this.handleSubmit} disable>Capture Payment </button>
                <button type="button" className="btn btn-outline-primary float-right mr-1" onClick={this.handleClose} data-dismiss="modal" aria-label="Close">Cancel</button>    
            </div>
          </Modal.Footer>
    </Modal>
         
          )
  }


render(){
    return(
        <div id=""><br/>
        {this.RepaymentSummaryModal(this.state.selected_payment)}
        <div className="container">
            <div className="form-row">
                <div className = "col-md-6">
                    <h3 className="lender_accounts">Recent Txns of {this.state.account_num}</h3>
                </div>

                <div className = "col-md-3">
                    {this.get_message()}
                </div>
               
                <div className = "col-md-3">
                    {/*<Button variant="primary" onClick={this.refreshPaymentsList}>Refresh Now</Button> &nbsp; */}
                    <Button variant={this.state.btn_variant} onClick={this.pauseRefresh}>{this.state.buttonName}</Button> 
                </div>
            </div>

            <div className="form-row mt-3">
                {this.state.last_updated && 
                <h5>Last updated : {this.state.last_updated} ({this.state.updated_since}s ago)</h5>
                }
            </div>

            
            <br/>
           </div>
            <div className={this.state.account_result}>
                <div id='datatable'>
                 
                    <table id ="statement_search" className='acc-stmt-table display strip row-border order-column list'>
                    <thead>
                        <tr>
                            <th>S.No</th>
                            <th></th> 
                            <th>Transaction Type</th>
                            <th>Credit</th>
                            <th>Debit</th> 
                            <th>Balance</th>
                            <th>Transaction ID</th>
                            <th>Description</th> 
                            <th>Transaction Date</th> 
                            <th>Matching FA ID</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody> 
                        {this.state.lenderTxnStmts}
                    </tbody>
                    </table>          
                </div>
            </div>
        </div>
        

      )


}
}


export default ListTodayAccountStmtTxnContainer;