import React from 'react';
import {updateStatus} from '../../../../actions/common_actions';
import  {lbl_status, style_status,variant_color,variant_value} from '../../../../helpers/common_helper';
import  {check_priv} from '../../../../helpers/storage_helper';
import FlowComponent from './FlowComponent';
import {dd_value} from '../../../../actions/common_actions';
import { Modal , Button,Badge} from 'react-bootstrap';
class StatusButton extends FlowComponent{
	
	componentWillMount(){
		console.log('componentWillMount')
		console.log(this.props.status)
		this.setState({status: this.props.status});
		
		var parent = this.props.parent;
		
		if(!parent){
			var entity = this.props.entity;

			parent = entity.slice(0,-1);
			if(parent == "borrower"){
				parent = "customer";
			}else if(parent == "loan_product"){
				parent = "product";
			}else if(parent == "loan_product_template"){
				parent = "template";
			}else if(parent == "acc_provider"){
				parent = "acc_prvdr";
			}else if(parent == "master_data_key"){
				parent = "data_key";
			}			

		}


		if(check_priv(parent , 'create')){
	        this.btn_style = false
	    }else{
	        this.btn_style = true
	    }
	}


	componentWillReceiveProps(props){
		console.log('componentWillMount')
		console.log(props.status)
		this.setState({status: props.status});
		
		var parent = props.parent;
		
		if(!parent){
			var entity = props.entity;

			parent = entity.slice(0,-1);
			if(parent == "borrower"){
				parent = "customer";
			}else if(parent == "loan_product"){
				parent = "product";
			}else if(parent == "loan_product_template"){
				parent = "template";
			}else if(parent == "acc_provider"){
				parent = "acc_prvdr";
			}else if(parent == "master_data_key"){
				parent = "data_key";
			}			

		}


		if(check_priv(parent , 'create')){
	        this.btn_style = false
	    }else{
	        this.btn_style = true
	    }
	}

	updateStatus = (event) => {
	const resp = window.confirm("Are you sure to change the status?");
	if(resp == false){
		return;
	}
	  let status = event.target.value.toLowerCase()+"d"; 
	  const request = {"id":event.target.id,status};
	  const request_data = {[event.target.name] : request}
	    updateStatus(this.req(request_data))
	       .then((response) => {
	        if(!response){return };
	            if(response.status === "success"){
	              this.setState({
	              	status
	              })
	              ///window.location.reload();
	            }else{
	              alert(response.message + "\n" + JSON.stringify(response.data));  
	            }
	          }
	        ); 
	      //this.setState({credit_score_factor_id: event.target.id, toView: true});
	   } 

	   render(){
	   	console.log(this.state.status)
	   	return(
	   		<>
	   		
	   		<td><Badge className="badge" pill variant={variant_color(this.state.status)}><span>{this.state.show_1l ? variant_value(this.state.status) : dd_value(this.state.status)}</span></Badge></td>
	        <td> <input id={this.props.id} type='button' name={this.props.entity} onClick={this.updateStatus} 
                              value={lbl_status(this.state.status)}  
                              className={style_status(this.state.status)} disabled={this.btn_style}/>
			</td>
	        </>
	   		);
	   }
}

export default StatusButton;