import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import '../../../styles/flow.css';
import {lbl_repayment_amt, loan_amt, lbl_amt, lbl_flow_fee_obj, lbl_dur,is_per_day_loan, lbl_flow_fee_duration} from '../../../helpers/product_helper';
import {percent_rnd} from '../../../helpers/common_helper';
import {Alert,Row, Col } from 'react-bootstrap';
import {format_date,rt_algn} from '../../../helpers/common_helper';
class ProductPanel extends FlowComponent {
	state = {
		variant: "primary"
	};
	componentWillMount(){

		if(this.props.parent == 'apply'){
			this.setState({variant: 'dark'})
		}else if(this.props.parent == 'loan_appl'){
			this.setState({variant: 'success'})
		}else if(this.props.parent == 'loan'){
			this.setState({variant: 'primary'})
		}
	}



	FloatAdvance1 = (loan_status) => (
				<>
					
					<Alert.Heading><b>Float Advance </b></Alert.Heading>
					{loan_status != "pending_approval" && 
						<Row>				
							
			            	<Col>Approval Date</Col>
							<Col as='b'>{format_date(this.props.product.loan_approved_date)}</Col>
							<Col></Col>
							<Col></Col>
						</Row>
					}
							<Row>
							<Col>Approver Name</Col>
							<Col as='b'>{this.props.product.loan_approver_name}</Col>
							<Col></Col>
							<Col></Col>
							
							</Row>	




						{(loan_status != "pending_disbursal" && loan_status != "pending_approval")
						&&
						<>	
						<Row>
							<Col>FA with fee</Col>
							<Col as='b'>{rt_algn(lbl_amt(this.props.product.due_amount, this.currency_code))}</Col>
							{loan_status == "approved" &&<>
							<Col></Col>
							<Col></Col>
							</>
						}
							{(loan_status != "approved" && loan_status != "pending_approval")
							&&
							<>

							<Col>FA Repaid</Col>
							<Col as='b' >{rt_algn(lbl_amt(this.props.product.paid_amount, this.currency_code))}</Col>
							</>}
						</Row>	
						
						</>
						}
					

					{loan_status != "pending_disbursal" && loan_status != "approved" && loan_status != "pending_approval"
					&&
					<>
						
						<Row> 
							
							<Col>Due Date</Col>
							<Col as='b'>{format_date(this.props.product.due_date)}</Col>
							{loan_status != "pending_disbursal" && loan_status != "approved"
							&&
								<>
								<Col>Disbursal Date</Col>
								<Col as='b'>{format_date(this.props.product.disbursal_date)}</Col>
                                
								</>
                   
							}
							
						</Row>	

						{this.props.product.provisional_penalty > 0 &&
						
						<Row>
							<Col>Provisional Penalty</Col>
							<Col as='b'>{rt_algn(lbl_amt(this.props.product.provisional_penalty, this.currency_code))}</Col>
							<Col>Penalty Paid</Col>
							<Col as='b'>{rt_algn(lbl_amt(this.props.product.penalty_collected, this.currency_code))}</Col>
						</Row>	
						}
						<Row>
							<Col>Current Due Amount</Col>
							<Col as='b'>{rt_algn(lbl_amt(this.props.product.current_os_amount, this.currency_code))}</Col>
							<Col></Col>
							<Col></Col>
						</Row>	
					</>	
				}
				<Row>
							{ this.props.product.repayment_date ?
							<>
								<Col>Repayment Date</Col>
								<Col as='b'>{format_date(this.props.product.repayment_date)}</Col>
							</> : <>
							<Col></Col>
							<Col></Col>
							</>
							}
							{ loan_status == "overdue" &&
							<>
								<Col></Col>
								<Col></Col>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
								<Col>Days Overdue</Col>
								<Col md={2} as='b'>{this.props.product.days_overdue} days ({this.props.product.penalty_days} penalty days) </Col>
							</>
							}
							<Col></Col>
							<Col></Col>
						</Row>
				</>
)


Product1 = (loan_status) => (
				<>
					
					<Alert.Heading><b>Product</b></Alert.Heading>
					<Row>
						<Col>Product Name </Col>
						<Col as='b'>{this.props.product.product_name}</Col>
						<Col>Credit Score </Col>
					    <Col as='b'>{this.props.credit_score} / 1000</Col>
					    
					</Row>		
					<Row> 
						<Col>FA Amount </Col>
						<Col as='b' className="right_align">{lbl_amt(loan_amt(this.props.product), this.currency_code)}</Col>
						<Col>Duration</Col>
						<Col as='b'>{lbl_dur(this.props.product.duration, this.props.product.flow_fee_duration)}</Col>
					</Row>		
					<Row>
						{/*<Col>Flow Fee For </Col>
						<Col as='b'>{lbl_flow_fee_duration(this.props.product.flow_fee_duration,  this.props.product.duration)}</Col>*/}
						<Col>Flow Fee </Col>
						<Col as='b'>{rt_algn(lbl_flow_fee_obj(this.props.product, this.currency_code))}</Col>
						<Col>Repayment Amount</Col>
						<Col as='b'>{rt_algn(lbl_repayment_amt(this.props.product, this.currency_code))}</Col>
					</Row>	
					
				{is_per_day_loan(this.props.product.flow_fee_duration)  
						&&
						 <div>
							*<i>Total repayment if float is utilized for all the {this.props.product.duration} day duration</i>
						<br/></div>
					}
					<hr/>
			</>
)

	
render(){
	console.log(JSON.stringify(this.props.product))

	return(
			<>
				<Alert variant={this.state.variant}>
				{this.Product1()}
				{/*this.props.parent != 'loan' && this.PastLoans1()*/}
				
				{/*{ this.props.parent == 'loan' && this.FloatAdvance1(this.props.product.status)}*/}
				{ this.props.parent != 'apply' && this.FloatAdvance1(this.props.product.status)}
				</Alert>
		</>
		);
	}

}

export default ProductPanel;