import React, { Component } from "react";
import { Button, FormGroup, FormControl, Form } from "react-bootstrap";
import "./../../../styles/login.css";
import {userService} from "./../../../actions/auth_actions";
import {Redirect} from 'react-router-dom';
import {get, add_all} from  './../../../helpers/storage_helper';
import {withRouter}  from  'react-router-dom';
class Login extends Component {
  constructor(props) {
    super(props);
     
    this.state = {
      email: "",
      password: "",
      submitted: false,
      loading: false,
      loggedin: false,
      error: ''
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  validateForm() {
    return this.state.email.length > 0 && this.state.password.length > 0;
  }

  handleChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }
  
  handleSubmit = event => {
    event.preventDefault();
        const email = this.state.email;
        const password = this.state.password;

        this.setState({ loading: true });
        userService.login(email,password)
                                       .then(response => {
                                                  if(!response) {return}
                                                  if(response.status === "success"){

                                                   // alert(JSON.stringify(response.data));
                                                     add_all(response)
                                                     // REMOVED const { from } = this.props.location.state || { from: { pathname: "/" } };
                                                     //REMOVED this.props.history.push({from});
                                                      //const { from } =  {from: { pathname: "/" }};
                                                               
                                                     this.props.onLogon();
                                                 }else{
                                                     this.setState({ error: response.status, loading: false });
                                                 }
                                                }
                                            );

        /*loginUser(credentials)
          .then((response) => {
          if(!response){return };

            
            
            if(response.access_token){
             this.props.handleLogin(event,true);
            }else{
              alert("Username or password is incorrect!");
              this.props.handleLogin(event,false);
            }
          }
        );*/
  }

 render() {
    if(!get('role_codes')){
           const { loading, error } = this.state;
          return (
            <div className="Login">
             <div className="row">
              <div className="col-md-12 text-center">
            <img src="/logo_blue.png" alt="/logo_blue.png" className="img-responsive"/>
              </div>
              </div> 
              <form onSubmit={this.handleSubmit}>
                <FormGroup controlId="email" bssize="large">
                  <Form.Label>Email</Form.Label>
                  <FormControl
                    autoFocus
                    type="email"
                    value={this.state.email}
                    onChange={this.handleChange}
                  />
                </FormGroup>
                <FormGroup controlId="password" bssize="large">
                  <Form.Label>Password</Form.Label>
                  <FormControl
                    value={this.state.password}
                    onChange={this.handleChange}
                    type="password"
                  />
                </FormGroup>
                <Button
                  block
                  bssize="large"
                  disabled={!this.validateForm()}
                  type="submit">
                  Login
                </Button>
                {loading &&
                                  <img src="data:image/gif;base64," 
                                  alt="Image not found" />
                              }
                   {error &&
                  <div className={'alert alert-danger'}>{error}</div>
              }
              </form><br/>
             </div>
          );
      }else{
        return(
          <Redirect to={{ pathname: '/' }} />
          );
      }
  }
}

export default withRouter(Login);