import React from 'react';
import { Redirect } from 'react-router-dom';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import {listLoanProduct, getSuggestion} from '../../../actions/loan_product_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import $ from 'jquery';
import StatusButton from '../../common/component/core/StatusButton';
import {rt_algn} from '../../../helpers/common_helper';
class searchProductContainer extends FlowContainer {
  state = {
     product_list: null,
     toView: false,
     search_res_div_style:"hide_div",
     product_id: null,
     query: '',
    results: []  
    
  }

  getInfo = () => {
/*
    axios.get(`${API_URL}?api_key=${API_KEY}&prefix=${this.state.query}&limit=7`)
      .then(({ data }) => {
        this.setState({
          results: data.data
        })
      })
*/

      getSuggestion(this.state.query)
         .then((response) => {
          if(!response){return };

              if(response.status === "success"){
                
                
                  this.setState({ results: response.data });
              }else{
                alert(response.message + "\n" + JSON.stringify(response.data));  
              }
            }
          );
  }

  handleInputChange = () => {
    this.setState({
      query: this.search.value
    }, () => {
      if (this.state.query && this.state.query.length > 1) {
        if (this.state.query.length % 2 === 0) {
          this.getInfo()
        }
      } else if (!this.state.query) {
      }
    })
  }

  handleClick = (event) => {
       
        this.setState({product_id: event.target.id, toView: true});
        //this.props.history.push("/market/view");
    }

  

  componentWillMount(){
      //const country_code = this.country_code;
    const data_prvdr_code = this.data_prvdr_code;
    listLoanProduct(this.req(null, data_prvdr_code))
        .then((response) => {
          if(!response){return };
              const product_list = response.data.map((product,i)=>
              <tr>
                <td key={i}> {product.product_name}</td>                
                <td key={i}> {product.data_provider}</td>
                <td key={i}> {product.lender}</td>
                <td key={i}> {product.credit_score_model}</td>
                <td key={i}> {product.interest_percent_per_day}</td>
                <td key={i}> {product.tenure_in_days}</td>
                <td key={i}> {rt_algn(product.max_loan_amount)}</td>
                <td key={i}> <StatusButton id={product.id} entity="loan_products" status={product.status}/></td>      
                <td key={i}> <input id={product.id} type='submit' className='' onClick={this.handleClick} value='View / Edit'  class='btn btn-primary' /></td>
              </tr>
                                              );
          this.setState({product_list: product_list});
        });
  }

  componentDidUpdate(){
    $('#list_product').dataTable({
        "order" : [],
     //"sPaginationType": "full numbers",
      "bAutoWidth": true,
      "bDestroy": true, 
    });
  }
  render(){
      
     if (this.state.toView === true) { 
           return <Redirect to={`/fa_product/product/view/${this.state.product_id}`} />
    }
   if (this.state.product_list != null) { 
    return( 
      <div className="">
      <form>
        <input
          placeholder="Search for..."
          ref={input => this.search = input}
          onChange={this.handleInputChange}
        />
      </form>
            <div id='datatable'>
                 <table id ='list_product' className="display strip row-border order-column list">
                        <thead>
                        <tr><th>Product Name</th>                        
                        <th>Data Provider</th>
                        <th>Lender</th>
                        <th>Credit Score Model</th>
                        <th>Interest % per day</th>
                        <th>Tenure in days</th>
                        <th>Max Loan Amount</th>
                        <th>Status</th>
                        <th>Action</th></tr></thead>
                        <tbody> 
                        {this.state.product_list}
                        </tbody>
                  </table>                  
            </div>
      </div>
      );  
    }
    return (
        <div><p>No Float Advance Products Available ! </p></div>
      );
  }
}
export default searchProductContainer;
