import React, { } from 'react';
import { Link } from 'react-router-dom';
import {set, get, sync_master_data} from '../../helpers/storage_helper';
import {append_request,hlpr_add_status} from '../../helpers/common_helper';
import {get_country_master_data} from '../../actions/common_actions';
import {listDataProvider} from '../../actions/data_provider_actions';
import Flag from "react-flags";
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import {Logo} from './../common/partials/logo';


import Modal from 'react-bootstrap/Modal';

export class SelectMarket extends React.Component {
  handleClose = (event) => {
    window.location.reload("/");
    this.setState({ modalBox : false });
  }

   componentWillMount(){
    //alert("SelectMarket");
    this.market_list = get('market_list');
         const ls_market = get('market')
      if(ls_market && (this.props.action == 'select' 
            || this.props.toChange == 'data_prvdr' 
            || this.props.toChange == 'data_prvdr_only')){
        this.setState({country_code: ls_market.country_code, market_style:"disable_div"})
        var req = {country_code: ls_market.country_code, time_zone: ls_market.time_zone, status: ""};
        req = hlpr_add_status(req);  
        this.list_data_prvdr_api(req);
  
      }
  
      //this.setState({ });
      var marketLinks = null
      if(this.props.toChange != 'data_prvdr_only'){
          marketLinks = this.market_list.map((market, index)=>
            <ul  key={index}>
              <div className="row">
                <Link to="#">
                  <Flag  name={market.country_code} format="png" pngSize={32} alt={market.country} basePath="/img/flags"/>  
                  &nbsp;
                  <a onClick={this.clickMarket.bind(this)} variant="primary" id={index}>{market.country} ({market.country_code})</a>
                </Link>
              </div>
            </ul>
          );
        
      }
  
      if(this.props.toChange == "data_prvdr_only" || this.props.toChange == "data_prvdr"){
        this.toChangeStr = 'data provider';
      }
      else if(this.props.toChange == "market"){
        this.toChangeStr = 'market / data provider';
      }
      var showX = false;
      if(this.props.action == 'change'){
        showX = true;
      }

      this.setState({marketLinks,  modalBox: true, showX} );
 }

clickDataProvider = (data_prvdr_code,data_prvdr_logo) =>{
    
    if(this.selectedMarket != null){
      set('market',this.selectedMarket);  
    }

    set('data_prvdr_code',data_prvdr_code);
    set('data_prvdr_logo',data_prvdr_logo);
    
    if(this.props.action == "select"){

      this.props.onSelect();
     }else if(this.props.action == "change"){
      window.location.reload("/")
     }


  }

  clickMarket = (event) => {
    //alert(event.target.id);
    
    this.selectedMarket = this.market_list[event.target.id];

    //localStorage.setItem('market', JSON.stringify(market));
    //localStorage.removeItem('data_prvdr_code');
    //localStorage.removeItem('data_prvdr_logo');
    const country_code = this.selectedMarket.country_code;
    const time_zone = this.selectedMarket.time_zone;
    const status = null;
    const req = {country_code, time_zone, status}
      get_country_master_data(req)
       .then((response) => {
          if(!response){return };
            if(response.status === "success"){
                sync_master_data(response.data.master_data);            
            }else{
              alert(response.message    + "\n"    +JSON.stringify(response.data));  
            }
          this.list_data_prvdr_api(req);  

        });     
   }

  list_data_prvdr_api(req) {
       listDataProvider(req)
              .then((response)=>{
                 if(!response){return };
                 if(response.status === "success"){
                   this.list_data_provider(req.country_code, response.data);
                 }
              });
    }
 
   list_data_provider = (country_code, dataprovider_list) => {
      const dataproviderLinks = dataprovider_list.map((dataprovider,index)=>
         <ul key={index}>
           <div className="row">
             <Link to="#">
               <Logo file_name={"t_"+  dataprovider.data_prvdr_logo} entity="data_prvdr" entity_code={dataprovider.data_prvdr_code} country_code={dataprovider.country_code} />&nbsp;&nbsp;
               <a onClick={this.clickDataProvider.bind(this,dataprovider.data_prvdr_code,dataprovider.data_prvdr_logo)} 
                   variant="primary">{dataprovider.name}</a>
             </Link>
           </div>
         </ul>
         );
       this.setState({country_code, dataproviderLinks, market_style:"disable_div"})
 
 
    }


  render() {
    
 
    return (
      <>
        <Modal show={this.state.modalBox} >
          <Modal.Header>
            <Modal.Title>Hello User!</Modal.Title>
               { this.state.showX && 
                <button type="button" onClick={this.handleClose} className="close" aria-label="Close">
                 <span aria-hidden="true">&times;</span>
                </button>
              }
          </Modal.Header>
          <Modal.Body>
                        <h4>Please select the {this.toChangeStr} you would like to administrate.</h4>
              {this.props.toChange == 'market' 
               && 
              <Card>
                 <Card.Header>
                    Choose Market
                 </Card.Header>
                    <Card.Body className={this.state.market_style}>{this.state.marketLinks}</Card.Body>
              </Card>}
              {this.state.country_code && 
                <Card>
                 <Card.Header>
                    Choose Data Provider from <Flag  name={this.state.country_code} format="png" pngSize={24} alt={this.state.country_code} basePath="/img/flags"/>
                    <b>{this.state.country_code}</b>
                 </Card.Header>
                    <Card.Body>{this.state.dataproviderLinks}</Card.Body>
              </Card>
              }
  
            {/*<Accordion defaultActiveKey='0'>
              <Card>
                 <Card.Header>
                    <Accordion.Toggle as={Button} variant="link" eventKey="0">
                    Choose Market
                    </Accordion.Toggle>
                 </Card.Header>
                 <Accordion.Collapse eventKey="0">
                    <Card.Body>{this.state.marketLinks}</Card.Body>
                 </Accordion.Collapse>
              </Card>
                <Card>
                 <Card.Header>
                    <Accordion.Toggle as={Button} variant="link" eventKey="0">
                    Choose Data Provider 
                    </Accordion.Toggle>
                 </Card.Header>
                 <Accordion.Collapse eventKey="0">
                    <Card.Body>{this.state.dataproviderLinks}</Card.Body>
                 </Accordion.Collapse>
              </Card>
             </Accordion>
           */}
          </Modal.Body>
        </Modal>
      </>
    );
  }
}

export default SelectMarket;
