import React from 'react';
import '../../../styles/flow.css';
import { Modal , Button} from 'react-bootstrap';
import FileUpload from '../../common/component/FileUpload';
import FlowComponent from '../../common/component/core/FlowComponent';
import {loadAgreementDetails, saveAgreement} from '../../../actions/agreement_actions';
import SignatureCanvas from 'react-signature-canvas';
import { Redirect } from 'react-router-dom';

class UploadAgreement extends FlowComponent {

	state = {
		viewPdf : false,
		sign_checked: false,
		upload_checked: false,
		sign_data: "",
		toapplyFA: false
	}
	componentWillMount(){
		
		const app_url = process.env.REACT_APP_URL;
		if(process.env.NODE_ENV !== 'development'){
            //this.root = "http://docs.google.com/viewer?embedded=true&url=" + app_url;
            this.root = ""; 
        }else{
            this.root = "http://localhost:8000"; 
        }
        
        var draft_aggr_doc_id = null;
		this.file_format = 'image/png';
		this.cust_id = this.props.match.params.cust_id;
		if(this.props.mode === "sign_draft"){
			//draft_aggr_doc_id = this.props.match.params.aggr_doc_id;
		}
		
		const request = {"cust_id": this.cust_id, "country_code": this.country_code};
		
    	loadAgreementDetails(this.req(request))
        .then((response) => {
        	this.resp(response);
          if(!response){return };
            if(response.status === "success"){                  
            	this.getSelectOptionsFromResult(response.data.master_agreements, "agreement", ['obj','name']);
            	if(draft_aggr_doc_id == null && response.data.draft_agreement != null){
	          		draft_aggr_doc_id = response.data.draft_agreement.aggr_doc_id
	          		
		        }
		        const draft_aggr_folder_rel_path = response.data.draft_aggr_folder_rel_path
		        const master_aggr_folder_rel_path = response.data.master_aggr_folder_rel_path
             	this.setState({draft_aggr_doc_id, draft_aggr_folder_rel_path, master_aggr_folder_rel_path});
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );
    	

	}

	handleReset = () => {
		//this.digitalSignature = false;
		if(this.aggr_type === "Master"){        
			this.aggr_doc_id = null
		}		
		this.setState({aggr_file_rel_path: null, upload_checked: false, sign_checked: false,
		 				viewPdf: false, file_upload: false, sign_data: null, agreement: null});
	}

	handleAggrTypeClick = (event) => {
	
    	if(event.target.checked){
    		var aggr_type = event.target.value;
    		if(aggr_type === "Draft"){

    			this.note = false;

    			this.aggr_doc_id = this.state.draft_aggr_doc_id;

    			event.target.id = 'aggr_doc_id';
    			event.target.value = this.state.draft_aggr_doc_id;
		 		this.handleSelfChangeState(event);
    		}
    		if(aggr_type === "no_draft"){
    			aggr_type = null;
    		}
    		this.setState({aggr_type});
    	}

    	if(aggr_type === "Master"){
    		this.note = true;
    	}
    		this.aggr_type = aggr_type;
    		this.handleReset();
    	

    }
	
   
    handleMasterAggrChange = (event) => { 
    	
		this.handleReset();

		var obj_str = event.target.value;
	 	var obj = JSON.parse(obj_str);
	 	this.handleSelfChangeState(event,obj.aggr_doc_id);
	 	this.aggr_doc_id = obj.aggr_doc_id;

		

  	}

  	handleUploadSignedAgr = (event) => {
  		this.submit_btn = true;
  		if(this.aggr_doc_id){
  			this.setState({file_upload: true, viewPdf: false, upload_checked: true, sign_checked: false});
		}else{
			alert("Please select an agreement");
		}	
  	}

  	handleDigitalSignature = (event) => {

  		if(this.aggr_doc_id){
		  	var aggr_file_rel_path = null
		  	this.setState({aggr_file_rel_path, file_upload: false,viewPdf: false, sign_checked: true, upload_checked: false, loading: true});

		  	
		  	if(this.state.aggr_type === "Master"){
		  		aggr_file_rel_path = this.state.master_aggr_folder_rel_path 
		  	
		  	}else if(this.state.aggr_type == "Draft"){
		  		aggr_file_rel_path = this.state.draft_aggr_folder_rel_path  
		  	}	
		  	aggr_file_rel_path += "/" + this.aggr_doc_id + ".pdf"

		  	this.setState({aggr_file_rel_path, viewPdf: true, loading: false});
	  	}else{
	  		alert("Please select an agreement");
	  	}
  	}

  	handleSubmitAggr = (event) => {
  		
  		const sign_type = "digital";
  		const file_of = "agreement";
  		//const file_data_type = "data-url";
  		const request = {"country_code": this.country_code,file_of,
  						 "cust_id": this.cust_id, "aggr_doc_id": this.aggr_doc_id, 
        				 "aggr_type": this.state.aggr_type,sign_type
        				};

  		if(this.state.file_upload){		
  			if(this.state.agreement == null){
  				alert("Please upload the file to submit.");
  				return;
  			}
  			var split_str = this.state.agreement.split('|');
  			request.sign_type="upload";
  			request.file_name = split_str[1];
  			request.file_type = split_str[0];
  			this.setState({
  				"file_name":request.file_name
  			})
  		}
  		/*else{
  			if(this.state.sign_data == null){
  				alert("Please capture your signature digitally to submit.");
  				return;
  			}
  			request.file_type = this.file_format;
  			request.file_data_type = "data-url";
  			request.file_data = this.state.sign_data;
  		}*/
    
    	
     
    	saveAgreement(this.req(request, true))
    	.then((response) => {
    		this.resp(response);
          if(!response){return };

            if(response.status === "success"){  
              var split_cust_id = this.cust_id.split('-');
              if(this.state.aggr_type == "Master"){
              	alert("Signed Agreement saved successfully -"+ this.aggr_doc_id+"-"+split_cust_id[1]);
              }else{
              	alert("Signed Agreement saved successfully -"+ this.aggr_doc_id);
              }
              this.setState({toapplyFA: true});
              //window.location.reload();
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );
    }

    handleClose = (event) => {
    	this.setState({modalBox: false});
  	}

    handleSignAgreement = (event) => {
    	this.setState({modalBox: true});
    }

    handleCaptureSignature = (event) => {
    	var resp = true;
    	event.target.value = this.signature.getTrimmedCanvas().toDataURL(this.file_format);   	
    	this.handleSelfChangeState(event);    	
    	this.setState({modalBox: false});
    	//alert("Are you sure to sign and submit Agreement.");
    	resp = window.confirm("Are you sure to sign and submit Agreement.")
    	if(resp == true){
    		
    		const sign_type = "digital";
	  		const file_of = "agreement";
	  		//const file_data_type = "data-url";

	  		const request = {"country_code": this.country_code,file_of,
	  		                 "file_name" :  this.aggr_doc_id,
	  						 "cust_id": this.cust_id, "aggr_doc_id": this.aggr_doc_id, 
	        				 "aggr_type": this.state.aggr_type,sign_type
	        				};
	   
  			//if(this.state.sign_data == null){
  			if(event.target.value == null){
  				alert("Please capture your signature digitally to submit.");
  				return;
  			}
  			request.file_type = this.file_format;
  			request.file_data_type = "data-url";
  			//request.file_data = this.state.sign_data;
  			request.file_data = event.target.value;
	  		
	        
	    	saveAgreement(this.req(request, true))
	    	.then((response) => {
	    		this.resp(response);
	          if(!response){return };
	            if(response.status === "success"){  
	              var split_cust_id = this.cust_id.split('-');
	              if(this.state.aggr_type == "Master"){
	              	alert("Signed Agreement saved successfully -"+ this.aggr_doc_id+"-"+split_cust_id[1]);
	              }else{
	              	alert("Signed Agreement saved successfully -"+ this.aggr_doc_id);
	              }
	              this.setState({toapplyFA: true});
	              //window.location.reload();
	            }else{
	              alert(response.message + "\n" + JSON.stringify(response.data));  
	            }
	          }
	        );
    	}else{
    		return;
    	}
    	
    }



	render(){

		if(this.state.toapplyFA){
			return(
				<Redirect to={`/fa_appl_for/${this.cust_id}`} />
				);
		}
		return(
		<div className="container">
			<div className="row">
				<h3>{this.cust_id} - Sign / Upload Agreement</h3>
			</div>
			<br/>
			
			<div className="row">
				<div className="col-md-4">
					{this.state.draft_aggr_doc_id && 
					<label>
					<input value="Draft" onClick={this.handleAggrTypeClick} type="radio" name="aggr"/>
					&nbsp;&nbsp;{this.state.draft_aggr_doc_id} (Customer Draft Agreement)
					</label>
					}
					{!this.state.draft_aggr_doc_id && <label><input name="aggr" value="no_draft" onClick={this.handleAggrTypeClick} type="radio"/>&nbsp;&nbsp;No Draft</label>
					}	
				</div>
				<div className="col-md-3">
					<label><input value="Master" onClick={this.handleAggrTypeClick} type="radio" name="aggr"/>
					&nbsp;&nbsp;Select from Master Agreements
					</label>
				</div>
			</div>
			
			<br/>
			{this.state.aggr_type &&	
			<>	
			<div className="row">
				<div className="col-md-4">
				
				<label>{this.state.aggr_type} Agreement Doc ID</label>
				
				</div>
				<div className="col-md-3">
				{(this.state.aggr_type === "Draft" && this.state.aggr_doc_id != null) &&
					<input type="text" id="aggr_doc_id" onChange={this.handleSelfChangeState} value={this.state.draft_aggr_doc_id} className="form-control" disabled/>
				}


				{this.state.aggr_type === "Master" &&
					<select id="aggr_doc_id" onChange={this.handleMasterAggrChange} className="form-control">
						{this.state.agreement_dd}
					</select>
				}	
				</div>
			</div>
					
				
			<br/>
	
			<div className="row">
				<div className="col-md-4">
					<label><input type="radio" name="agreement" value="upload" onClick={this.handleUploadSignedAgr} defaultChecked={this.state.upload_checked}/>&nbsp;&nbsp;Upload Signed Copy of {this.state.aggr_type} Agreement</label>
				</div>
				<div className="col-md-3">
					<label><input type="radio" name="agreement" value="sign" onClick={this.handleDigitalSignature} defaultChecked={this.state.sign_checked} />&nbsp;&nbsp;Digitally Sign {this.state.aggr_type} Agreement</label>
				</div>
			</div>
	
			<br/>
			</>
			}

				{this.state.file_upload &&
				<div className="row">
					<div className="col-md-6">
			
		                 <FileUpload id="agreement" aggr_doc_id={this.aggr_doc_id}
							cust_id={this.cust_id} label= "Agreement" type="image" mode='create'
							onCreateUpload={this.handleSelfChangeState}/>
				
					</div>
				</div>
				}
				{this.state.loading &&
					<span>Please wait while Agreement PDF is loaded.</span>
				}
				{this.state.viewPdf && 
					<>
					<div className="row" id="float_style">
						<input type="button" disabled={this.state.calling_api} value="Sign Agreement" onClick={this.handleSignAgreement} className="btn btn-primary" />
					</div>
					<div className="row">


						<h4>Master Agreement Document</h4>&nbsp;&nbsp;&nbsp;
						{this.note &&
							<span className="cust_details">Note : Customer details will be automatically filled once document is digitally signed </span>
						}


							{/*<object type="application/pdf" className="aggr_style" data={this.root + this.state.aggr_file_rel_path}></object>*/}
							<iframe className="aggr_style" src={this.root + this.state.aggr_file_rel_path} ></iframe>

							{/*<object className="aggr_style" data={this.root + this.state.aggr_file_rel_path} alt={this.state.aggr_file_rel_path}></object>*/}


							{/*<object className="aggr_style" data={this.root + this.state.aggr_file_rel_path} alt={this.state.aggr_file_rel_path}></object>*/}



				
					</div>
					<br/>
					<div className="row" id="float_style">
						<input type="button" disabled={this.state.calling_api} value="Sign Agreement" onClick={this.handleSignAgreement} className="btn btn-primary" />
					</div>
					</>
				}
				

			<br/>
			{this.submit_btn &&
				<div className="row">
					<input type="button" disabled={this.state.calling_api} onClick={this.handleSubmitAggr} value="Submit" className="btn btn-primary" />
				</div>
			}

			<Modal show={this.state.modalBox} onHide={this.handleClose} dialogClassName="modal-dialog-sign" >
			        <Modal.Header closeButton show={this.state.modalBox} onHide={this.handleClose}>
			            <Modal.Title>Sign here</Modal.Title>
			        </Modal.Header>

			        <Modal.Body>
			       
				      <SignatureCanvas penColor="blue" canvasProps={{width:250, height:120,className: 'signature_style'}} 
                              ref={(ref) => {this.signature = ref}} />
                 
                            

			        </Modal.Body>

			        <Modal.Footer>
			        <div className="center">
			        	<Button  variant="primary" id="sign_data" onClick={this.handleCaptureSignature}>Capture Signature</Button>
			        	</div>
			        	<div className="center_btn">
			        	<Button variant="secondary" onClick={(e) => {this.signature.clear()}}>Clear</Button>
			        	</div>
			        	
				        <Button variant="secondary" onClick={this.handleClose}>Close</Button>
			        </Modal.Footer>
			    </Modal>



		</div>
	
			);
	}
}

export default UploadAgreement;
