import AccountProviderContainer from './../UI/account_provider/container/account_provider_container';
import ListAccProviderContainer from './../UI/account_provider/container/list_acc_prvdr_container';
import ProductTemplateContainer from './../UI/loan_product/container/product_template_container';
import ProductContainer from './../UI/loan_product/container/product_container';
import listProductTemplateContainer from './../UI/loan_product/container/list_product_template_container';
import listProductContainer from './../UI/loan_product/container/list_product_container';
import searchProductContainer from './../UI/loan_product/container/search_loan_product';
import MasterDataKeyContainer from './../UI/master_data/container/master_data_key_container';
import ListMasterDataKeyContainer from './../UI/master_data/container/list_master_data_key_container';
import MasterDataContainer from './../UI/master_data/container/master_data_container';
import AppUserContainer from './../UI/user/container/app_user_container';
//import ListAppUserContainer from './../UI/user/container/list_app_user_container';
import CreateMasterAgreement from './../UI/agreement/component/CreateMasterAgreement';
import ListAgreementContainer from './../UI/agreement/container/list_agreement_container';
import ScoreModels from './../UI/score_models/component/ScoreModels';
import ScoreFactors from './../UI/score_models/component/ScoreFactors';
import CSWeightage from './../UI/score_models/component/CSWeightage';
import ScoreResultConfig from './../UI/score_models/component/ScoreResultConfig';
import KPIReportContainer from './../UI/reports/container/kpi_report_container';
import CommonReportContainer from './../UI/reports/container/common_report_container';

import GrowthChart from './../UI/reports/component/GrowthChart';
import CreateAgreement from './../UI/borrower/component/CreateAgreement';
import UploadAgreement from './../UI/borrower/component/UploadAgreement';
//import SearchAccountStmtTxnContainer from './../UI/account/container/search_account_stmt_txn_container';
import ListAccountTxnContainer from './../UI/account/container/list_account_txn_container';

import ListTodayAccountStmtTxnContainer from './../UI/account/container/list_today_account_stmt_txn_container';
import CheckIn from './../UI/field_visit/component/CheckIn';


export const MarketRoutes                 = (
    <>
                <Route path={`/market/create`} exact  component={() => <MarketContainer mode='create' title='Create Market'/>}/>
                <Route path={`/market/list`} exact component={ListMarketContainer}/>
                <Route path={`/market/view/:country_code`} exact  component={(props) => <MarketContainer mode='view' title="View/Edit Market" {...props} />}/>
    </>                
)

export const LenderRoutes                 = (
    <>
                <Route path={`/lender/create`} exact  component={() => <LenderContainer mode='create' title="Create Lender"/>}/>
                <Route path={`/lender/list`} exact component={ListLenderContainer}/>
                <Route path={`/lender/view/:lender_code`} exact  component={(props) => <LenderContainer mode='view' title="View/Edit Lender" {...props} />}/>
                <Route path={`/lender/accounts`} exact  component={LenderAccountStmtTransactionsContainer}/>
   </>          
)
export const DataPrvdrRoutes                 = (
    <>
                <Route path={`/data_provider/create`} exact  component={() => <DataProviderContainer mode='create' title="Create Data Provider"/>}/>
                <Route path={`/data_provider/list`} exact component={ListDataProviderContainer}/>
                <Route path={`/data_provider/view/:data_prvdr_code`} exact  component={(props) => <DataProviderContainer mode='view' title="View/Edit Data Provider" {...props}/>}/>
               
   </>                
)

export const AccPrvdrRoutes                 = (
    <>
                <Route path={`/account_provider/create`} exact  component={() => <AccountProviderContainer mode='create' title="Create Account Provider"/>}/>
                <Route path={`/account_provider/list`} exact  component={() => <ListAccProviderContainer/>}/>
   </>                
)

export const ProductRoutes                 = (
    <>
                <Route path={`/fa_product/template/create`} exact  component={() => <ProductTemplateContainer mode='create' title="Create FA Product Template"/>}/>
                <Route path={`/fa_product/create`} exact  component={() => <ProductContainer mode='create' title="Create FA Product"/>}/>
                <Route path={`/fa_product/template/view/:product_template_id`} exact  component={(props) => <ProductTemplateContainer mode='view' title="View/Edit FA Product Template" {...props}/>}/>
                <Route path={`/fa_product/view/:product_id`} exact  component={(props) => <ProductContainer mode='view' title="View/Edit FA Product" {...props}/>}/>
                <Route path={`/fa_product/template/list`} exact component={listProductTemplateContainer}/>
                <Route path={`/fa_product/list`} exact component={listProductContainer}/>
                <Route path={`/fa_product/search_product`} exact component={searchProductContainer}/>
   </>                
)


export const MasterDataRoutes                 = (
    <>              
                <Route path={`/master_data_key/add`} exact component={() => <MasterDataKeyContainer/>}/>
                <Route path={`/master_data_key/list`} exact component={() => <ListMasterDataKeyContainer/>}/>
                <Route path={`/master_data/add`} exact component={MasterDataContainer}/>               
    </>
)           

export const AppUserRoutes = (
    <>  
                <Route path={"/app_user/create"} exact component={AppUserContainer}/>

    </>
)

export const AgreementRoutes = (
    <>              
                <Route path={`/agreement/create`} exact component={() => <CreateMasterAgreement title="Create Master Agreement" id="master_agreement"/>}/>
                <Route path={`/agreement/list`} exact component={() => <ListAgreementContainer/>}/>  
                <Route path={`/agreement/create/:cust_id`} exact component={(props) => <CreateAgreement id="create_agreement" {...props}/>}/> 
                <Route path={`/agreement/list/:cust_id`} exact component={(props) => <CreateAgreement mode="list" id="create_agreement" {...props}/>}/> 
                
                <Route path={`/agreement/upload/:cust_id`} exact component={(props) => <UploadAgreement {...props}/>}/>            
                <Route path={`/agreement/upload/:cust_id/:aggr_doc_id`} exact component={(props) => <UploadAgreement mode="sign_draft" {...props}/>}/>            
    </>
)   

export const ScoreRoutes = (
    <>              
                <Route path={`/score_models`} exact component={() => <ScoreModels id="score_model" />}/>
                <Route path={`/score_models/weightage/:score_model?/:score_model_name?`}  component={(props) => <CSWeightage id="csw_comp" {...props}/>}/>
                <Route path={`/score_models/result_config/:score_model?`}  component={(props) => <ScoreResultConfig id="result_config" {...props}/>}/>
                <Route path={`/score_factors`} exact component={() => <ScoreFactors />}/>
                
                
    </>
) 

export const Reports         = (
    <>
              
                <Route path={`/report/monthly_kpi_report`} exact component={(props) => <KPIReportContainer type="monthly" {...props}/>}/>
                <Route path={`/report/current_kpi_report`} exact component={(props) => <KPIReportContainer type="current" {...props}/>}/>
                <Route path={`/report/portfolio_quality`} exact component={(props) => <CommonReportContainer report_type="portfolio_quality" {...props}/>}/>
                <Route path={`/report/product_takeup`} exact component={(props) => <CommonReportContainer report_type="product_takeup" {...props}/>}/>
                <Route path={`/report/daily_activity`} exact component={(props) => <CommonReportContainer report_type="daily_activity" {...props}/>}/>
                <Route path={`/report/growth_chart`} exact component={(props) => <GrowthChart id="growth_chart" {...props}/>}/> 
                <Route path={`/report/growth_chart_for/:cust_id`} exact component={(props) => <GrowthChart id="growth_chart" {...props}/>}/> 
      
   </>                
)

export const AccountRoutes                 = (
    <>
                {/*<Route path={`/acc_txns/create:acc_id`} exact component={(props) => <CreateAccountTxnContainer {...props}/>}/>*/}
                <Route path={`/acc_txns/:entity/:entity_code/:acc_id`} exact component={(props) => <ListAccountTxnContainer {...props}/>}/>
                <Route path={`/account/:acc_id/statement`} exact component={(props) => <ListTodayAccountStmtTxnContainer {...props}/>}/>

     </> 

)

export const FieldvisitRoutes                 = (
    <>
                <Route path={`/field_visit/search`} exact component={(props) => <FieldVisitContainer {...props}/>}/>
                 <Route path={`/checkin/:cust_id`} exact component={(props) => <CheckIn {...props}/>}/>

     </> 
          
)


