import React from 'react';
import {viewBorrower} from '../../../actions/borrower_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import BorrowerPanel from '../component/BorrowerPanel';
import PersonPanel from '../../common/component/PersonPanel';
import AddressPanel from '../../common/component/AddressPanel';
import '../../../styles/flow.css';
import {Alert} from 'react-bootstrap';

class ViewIndividualBorrowerContainer extends FlowContainer {
	state = {
		customer : {},
		owner_person: {},
		contact_person: {},
		biz_address: {}
	}
	componentWillMount(){
		
		this.cust_id = this.props.match.params.cust_id;
		viewBorrower(this.req({cust_id: this.cust_id, screen: "view"}, true))
			.then((response) => {

				
			if(!response){return };
			
			this.contact_person = null;
			this.owner_address = null;
			if(response.data.owner_person_id != response.data.contact_person_id){
				
				this.contact_person = response.data.contact_person
			}
			if(response.data.owner_address_id != response.data.biz_address_id){
				if(response.data.owner_person){
					this.owner_address = response.data.owner_person.owner_address	
				}
				
			}
			
			this.setState({customer : response.data,
						   owner_person : response.data.owner_person,
						   biz_address: response.data.biz_address,
						   
						   });
			
			}
		);
	}

	render(){
		console.log("customer--"+this.state.customer)
		
		return(
			<div className="container"><br/>


				<BorrowerPanel customer={this.state.customer} address={this.state.biz_address}/>

				{this.state.owner_person && <PersonPanel data={this.state.owner_person} title="Business Owner Person" />}
				{this.contact_person ? 
					<PersonPanel data={this.contact_person} no_photo='true' title="Contact Person" />	
					:
					<label><Alert.Heading><b>Contact Person : Same as Business Owner Person</b></Alert.Heading></label>
				}

				{this.state.biz_address && <AddressPanel data={this.state.biz_address} title="Business Address" />}
				{this.owner_address ? 
					<AddressPanel data={this.owner_address} title="Owner Address" />	
					:
					<label><Alert.Heading><b>Owner Address : Same as Business Address</b></Alert.Heading></label>
				}
			    
			</div>
			);
	}

}

export default ViewIndividualBorrowerContainer;