import {call_fetch, admin_path} from './../helpers/fetch_helper';

export const createDataProvider = request  => {
   return call_fetch(admin_path + '/data_provider', request);
}

export const viewDataProvider = request  => {
   return call_fetch(admin_path + '/data_provider/view', request);
}

export const listDataProvider = request  => {
   return call_fetch(admin_path + '/data_provider/list', request);
}

export const listCCAPayments = request  => {
   return call_fetch(admin_path + '/data_provider/cca/payments', request);
}