import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
//import {getAccountTxnsType} from '../../../actions/account_txn_actions';
import {getMasterData} from '../../../actions/common_actions';
import {Button } from 'react-bootstrap';
import DatePicker from 'react-datetime';
import moment from 'moment';
import '../../../styles/flow.css';
import {getRefAccounts} from '../../../actions/account_txn_actions';
import $ from 'jquery';

class StatementSearch extends FlowComponent {

  state = {
    section1: "show_div", 

  }

componentWillMount()
{
        /*  const request = {data_key: "account_transaction_type"};
        getAccountTxnsType(this.req(request))
        .then((response) => {
          if(!response){return };
            if(response.status === "success"){
                this.getSelectOptionsFromResult(response.data.list, "account_txn_type", ["data_code", "data_value"]);
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );*/
        this.setOptionsToStateFromApi("lender");   

      const request = {"status": 'enabled', data_key: "account_transaction_type"}
      const request_data = {"master_data": request};

      getMasterData(this.req(request_data))
         .then((response) => {
          if(!response){return };
            if(response.status === "success"){
                this.getSelectOptionsFromResult(response.data, "account_txn_type", ["data_code", "data_value"]);
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );

  }

  handleResetClick = (event) => {
    alert("called")
    this.handleReset();
  }


  handleSection2 = (value) =>{
      value = moment(value).format("YYYY-MM-DD");
      if(value.target){
        const event = value;
        value = event.target.value
      }
      if(value != "")
      {
        this.setState({section2: "disable_div"});
      }

      else{
        this.setState({section2:"show_div"});
      }
    }


  handleFields = (event) =>{
    
    if(event.target.value!= "")
    {
      this.setState({section1: "disable_div"});
    }
    else if(event.target.value == "")
    {
    this.setState({section1:"show_div"}); 
    }

    this.handleChange(event);
  }
 

   loadAccounts = (event) => {
    const lender_code = event.target.value
  
    var account = {lender_code, 
          "country_code": this.country_code, 
          lender_data_prvdr_code : this.data_prvdr_code,
        to_recon : true};
    
    getRefAccounts(this.req({account}))
      .then((response) => {
        if(!response){return };
        if(response.status === "success"){
          if(response.data != "")
          {
            this.getSelectOptionsFromResult(response.data, "lender_accounts", ['id','name']);
          }     
        
        }else{
           
          alert(response.message + "\n" + JSON.stringify(response.data));  
        }
      }

    );
      

  }
      
    render(){
      return(
        <div className="container">
          
        <div className = "form-row">
            <div className = "form-group col-2 ">
             <label className = "form-label">Lender</label><font className="text-danger">   *  </font>
                <select id="lender_code" defaultValue="UFLW" onChange={this.loadAccounts} className="form-control" type="text" required="required">
                  {this.state.lender_dd}
                </select>
            </div>
            <div className = "form-group col-3">
              <label className = "form-label">Accounts</label><font className="text-danger">   *  </font>
                <select id="account_id" className="form-control" required="required" onChange={this.handleChange} >
                    {this.state.lender_accounts_dd}                               
                </select>
            </div>
            <div className = {`form-group col-2 ${this.state.section1}`}>
              <label className = "form-label">Start Date</label><font className="text-danger">   *  </font>
              <DatePicker closeOnSelect={true}  onChange={(value) => {this.handleDateChange("stmt_txn_date__from", value);this.handleSection2(value);}}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"/>

            </div>
            <div className = {`form-group col-2 ${this.state.section1}`}>
              <label className = "form-label">End Date</label><font className="text-danger">   *  </font>
                <DatePicker closeOnSelect={true}  onChange={(value) => {this.handleDateChange("stmt_txn_date__to", value);this.handleSection2(value);}}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"/>

            </div>
        </div>
        <div className = "form-row">
           
            <div className = {`form-group col-2 ${this.state.section1}`}>
                <label className = "form-label">Statement Transaction Type</label>
                <select id="stmt_txn_type" className = "form-control" onChange={this.handleChange}>
                {this.getSelectOptions("stmt_txn_type")}
                </select>
            </div>
            <div className = {`form-group col-3 ${this.state.section1}`}>
                <label className = "form-label">Recon Status</label>
                <select id="recon_status" className = "form-control" onChange={this.handleChange} >
                {this.getSelectOptions("recon_status")}
                </select>
            </div>
            <div className = {`form-group col-2 ${this.state.section2}`}>
                <label className = "form-label">Statement Transaction ID</label> 
                <input id ="stmt_txn_id" className = "form-control" onChange={(e) => {this.handleFields(e);this.handleChange(e);}} required/>
            </div>
        </div>
        
        <div className="form-row justify-content-md-center">
            <Button className="btn btn-primary " onClick={this.props.onlist}  >Search</Button> &nbsp;   
            {/*<Button type="reset" onClick={this.handleResetClick}>Reset</Button>*/}
        </div>
    </div> 
    )
    }

}
export default StatementSearch;