import {call_fetch, admin_path} from './../helpers/fetch_helper';


export const createRelationshipManager = request => {
   return call_fetch(admin_path + '/rel_mgr', request);
}

export const viewRelationshipManager = request => {
   return call_fetch(admin_path + '/rel_mgr/view', request);
}

export const listRelationshipManager = request => {
   return call_fetch(admin_path + '/rel_mgr/list', request);
}

