import {call_fetch, admin_path} from './../helpers/fetch_helper';

export const createAccount = request   => {
   return call_fetch(admin_path + '/account/create', request);
}

export const listAccount = request  => {
   return call_fetch(admin_path + '/account/list', request);
}

export const updatePrimaryAccount = request  => {
   return call_fetch(admin_path + '/account/make_primary', request);
}

export const viewAccount = request  => {
   return call_fetch(admin_path + '/account/view', request);
}

export const updateAccount = request  => {
   return call_fetch(admin_path + '/account/update', request);
}

