import React from 'react';
import '../../../styles/flow.css';
import FlowContainer from '../../common/container/core/FlowContainer';
import $ from 'jquery';
import {listAgreement} from '../../../actions/agreement_actions';
import {dd_value} from '../../../actions/common_actions';
import StatusButton from '../../common/component/core/StatusButton';
import { format_date_tbl} from '../../../helpers/common_helper';
import { Link } from 'react-router-dom';

class ListAgreementContainer extends FlowContainer {

	state = {
     agreementList: null
  	}

  	componentWillMount(){
  		
  		listAgreement(this.req(null, true))
        .then((response) => {
          if(!response){return };
         
          const agreementList = response.data.map((agreement,i)=>

              <tr key={i}>
                <td> {agreement.name}</td>
                <td> {agreement.data_prvdr_code}</td>
                <td> {agreement.lender_code}</td>
                <td> {agreement.aggr_doc_id}</td>
                <td> {format_date_tbl(agreement.valid_from)}</td>
                <td> {format_date_tbl(agreement.valid_upto)}</td>
                <StatusButton id={agreement.id} entity="master_agreements" status={agreement.status}/>
                <td> {agreement.aggr_file_rel_path ? 
                             <Link to={agreement.aggr_file_rel_path} target="_blank">View PDF</Link>
                             :
                             <label>NA</label>}
                </td>
              </tr>
          );
          
          this.setState({agreementList: agreementList});



          $('#list_agreement').removeAttr('width').dataTable({
                  "order" : [],
                  "columns": [
                       {"width" : "10%"},
                       {"width" : "10%"}, 
                       {"width": "10%"},
                       {"width": "20%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "10%"}                      
                     ],
                  dom:'Bfrtip',
                  buttons: [
                  'csv','excel','print'
                  ]
              });
           });

  	}

  	render(){
  		return(
  			<div id='datatable'>
           	<h4>Agreement List</h4> 
             <table id ='list_agreement' className="display strip list">
                      <thead>
	                      <tr>
                        <th>Name</th>
                        <th>Data Provider</th>
	                      <th>Lender</th>
	                      <th>Agreement Doc ID</th>
	                      <th>Valid from</th>
	                      <th>Valid to</th>
	                      <th>Status</th>
	                      <th>Action</th>
	                      <th>View</th>
	                      </tr>
                      </thead>
                      <tbody> 
                      {this.state.agreementList}
                      </tbody>
             </table>        
        </div>


  			);
  	}

}

export default ListAgreementContainer;