
import React from 'react';
import { Redirect} from 'react-router-dom';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import {listLoanProductTemplate} from '../../../actions/loan_product_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import  {lbl_flow_fee_obj, lbl_flow_fee_duration} from './../../../helpers/product_helper';
import StatusButton from '../../common/component/core/StatusButton';
import {rt_algn} from '../../../helpers/common_helper';
import {ViewEdit} from '../../common/partial';
import $ from 'jquery';

class listProductTemplateContainer extends FlowContainer {

  state = {
     templateList: null,
     toView: false,
     template_id: null ,

  }
  handleClick = (event) => {
        this.setState({template_id: event.target.id, toView: true});

    }


  componentWillMount(){
    listLoanProductTemplate(this.req(null))
        .then((response) => {
          if(!response){return };
              const templateList = response.data.map((template,i)=>
              <tr key={i}>      
                <td data-sort={template.max_loan_amount}> {rt_algn(template.max_loan_amount,this.currency_code)}</td>    
                <td> {template.duration} day(s)</td>                      
                <td> {template.flow_fee_type} </td>
                <td data-sort={template.flow_fee}> {rt_algn(lbl_flow_fee_obj(template, this.currency_code))} </td>
                <td> {lbl_flow_fee_duration(template.flow_fee_duration, template.duration)} </td>
                <StatusButton id={template.id} entity="loan_product_templates" status={template.status}/>

                <td><ViewEdit id={template.id} entity="template"  onClick={this.handleClick}/></td>
                {/*<td key={i}> <input id={template.id} type='submit' onClick={this.handleClick} value='View / Edit'  className='btn btn-primary' /></td>*/}

              </tr>
               );
          this.setState({templateList: templateList});
           $('#list_template').removeAttr('width').dataTable({
              "order" : [],
                  "columns": [

                       {"width" : "5%"}, 
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"}                                            
                     ],

                  dom:'Bfrtip',
                  buttons: [
                  'csv','excel','print'
                  ]     
              });
        });
  }


  render(){
      
     if (this.state.toView === true) { 
           return <Redirect to={`/fa_product/template/view/${this.state.template_id}`} />
    }

   if (this.state.templateList != null) { 
      
    return( <div id='datatable'>
            <h4>List of FA Products Template</h4>
             <table id ='list_template' className="display strip row-border order-column list">
                      <thead>                      
                      <tr>
                        <th>FA Amount</th>      
                        <th>FA Duration</th>       
                        <th>Flow Fee Type</th>
                        <th>Flow Fee</th>
                        <th>Flow Fee For</th>
                        <th>Status</th>
                        <th>Action</th>
                        <th>Action</th></tr></thead>
                      <tbody> 
                      {this.state.templateList}
                      </tbody>

             </table>          
        </div>
      ); 
    }
      return (
        <div><p>No Loan product template Available ! </p></div>

      );
  }
}
export default listProductTemplateContainer;
