import React from 'react';
import { Redirect } from 'react-router-dom';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import {listRelationshipManager} from '../../../actions/relationship_manager_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import {person_name} from '../../../helpers/common_helper';
import StatusButton from '../../common/component/core/StatusButton';
import {dd_value} from '../../../actions/common_actions';
import {ViewEdit} from '../../common/partial';
import $ from 'jquery';;

class listFlowRelationshipManagerContainer extends FlowContainer {

  state = {
     relationshipManagerList: null,
     toView: false,
     relationshipManager_id: null 
    
  }

  handleClick = (event) => {
        this.setState({relationshipManager_id: event.target.id, toView: true});
    }

  componentWillMount(){
      const request = {"associated_with":"FLOW"};
    listRelationshipManager(this.req(request))
        .then((response) => {
          if(!response){return };
          const relationshipManagerList = response.data.map((relationshipManager,i)=>
              <tr key={i}>
                <td> {person_name(relationshipManager)}</td>
                <td> {relationshipManager.mobile_num}</td>
                <td> {relationshipManager.email_id}</td>
                <td> {relationshipManager.designation}</td>
                <StatusButton id={relationshipManager.id} parent="flow_rel_mgr" entity="persons" status={relationshipManager.status}/>

                <td><ViewEdit id={relationshipManager.id} entity="flow_rel_mgr"  onClick={this.handleClick}/></td>
                {/*<td key={i}> <input id={relationshipManager.id} type='submit' onClick={this.handleClick} value='View / Edit'  className='btn btn-primary' /></td>*/}

              </tr>
                                              );
          
          this.setState({relationshipManagerList: relationshipManagerList});

              $('#list_relationshipManager').removeAttr('width').dataTable({
                  "order" : [],
                  "columns": [
                       {"width" : "15%"}, 
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "10%"} 
                     ],

                  dom:'Bfrtip',
                  buttons: [
                  'csv','excel','print'
                  ]
            
              });

        });
  }


  render(){
      
     if (this.state.toView === true) { 
           return <Redirect to={`/relationship_manager/flowrlspmanager/view/${this.state.relationshipManager_id}`} />
    }

   if(this.state.relationshipManagerList != null) { 
      
    return( <div id='datatable'>
            <h4>{this.props.title || "Relationship Manager List"}</h4> 
             <table id ='list_relationshipManager' className="display strip row-border order-column list">
                      <thead>
                      <tr>
                      <th>Name</th>
                      <th>Mobile Number</th>
                      <th>Email ID</th>
                      <th>Designation</th>
                      <th>Status</th>
                      <th>Action</th>
                      <th>Action</th></tr></thead>
                      <tbody> 
                      {this.state.relationshipManagerList}
                      </tbody>
             </table>         
        </div>
      );

    }
      return (
        <div><p>No Relationship Manager Available ! </p></div>
        
      );
  }
}

export default listFlowRelationshipManagerContainer;
