export function variant_color(value)
{
	if(value === "Probation" || value === "Condonation")
	{
		return 'secondary';
	}else if(value === "Regular" || value == "in_progress")
	{
		return 'warning';
	}
	else if(value === "enabled" || value == "completed")
	{
		return 'success' 
	}else if(value === "disabled" ||  value == "pending")
	{
		return 'danger';
	}else{
		return 'info'
	}
} 

export function cust_score_color(value)
{
	if(value === "eligible"){
		return 'success' 	
	}else if ( value === "requires_flow_rm_approval"){
		return "primary"
	}else {
		return "danger"
	}

	}

