export function fa_product_action(is_eligible, result_code){
	if(is_eligible == true){
		return "Select";
		
	}else if(is_eligible == false){
		if(result_code === "requires_flow_rm_approval"){
			return "Request Approval";
		}
		else{
			return result_code;
		}
	}
}



export function get_eligibility(is_eligible, result_code, approver){
	if(is_eligible == true){
		return "Eligible";
		
	}else if(is_eligible == false){
		if(result_code === "requires_flow_rm_approval"){
			return "Need "+ approver +"'s Approval"
		}else if(result_code == "ineligible"){
			return "Ineligible";
			
		}
		else if(result_code){
			return capitalize_first_letter(result_code)
		}else{
			return result_code;
		}
	}
}


