import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import {format_date} from '../../../helpers/common_helper';
import FileUpload from './FileUpload';
import DatePicker from 'react-datetime';
import {Edit} from '../../common/partial';

class Person extends FlowComponent {
   state = {
    comp_name: "person",
    initials:null,
    date: new Date()
  }

  handleKeyUp = targetId => (event) => {
         this.props.onComponentChange(event,this.props.id);
         const value = event.target.value.charAt(0).toUpperCase();
         document.getElementById(targetId).value = value;
         this.props.data[targetId] = value;
  }



  render(){
 
    return (
      <div id="">
          <div className="row">
            <div className="col-md-3"><h4>{this.props.title ? this.props.title : ""}</h4></div>

            <div className={`col-md-1 ${this.state.edit_button_style}`}>
              <Edit entity={this.props.entity} onClick={this.handleEdit}/>              
            </div>
            <div className={`col-md-1 ${this.state.save_button_style}`}>
              <input  onClick={this.handleSave} type="button" className="btn btn-success" value="Save"/>
            </div>
          </div>
          <div className={this.state.div_style}>
            <div className="row">
                <div className="col-md-3">
                  <span>First Name</span><font className="text-danger">   *  </font>
                  <input id="first_name" value={this.props.data.first_name}  onKeyUp={this.handleKeyUp("initials")} onChange={this.toUpperHandleChange} type="text" className="upperCase form-control" required="required"/>
                </div>
                <div className="col-md-3">
                  <span>Middle Name</span>
                  <input id="middle_name" onChange={this.toUpperHandleChange}  value={this.props.data.middle_name} className="form-control" type="text"  />
                </div>
                <div className="col-md-3">
                  <span>Last Name</span><font className="text-danger">   *  </font>
                  <input id="last_name" value={this.props.data.last_name}  onChange={this.toUpperHandleChange} className="form-control" type="text" />
                </div>  
                <div className="col-md-3">
                  <span>Initials</span>
                  <input id="initials" value={this.props.data.initials}  onChange={this.toUpperHandleChange} className="form-control" type="text"  />
                </div>          
            </div><br/>
            <div className="row">
                <div className="col-md-3">
                  <span>Date of birth</span>

                     <DatePicker  value={format_date(this.props.data.dob)} closeOnSelect={true} onChange={(value) => {this.handleDateChange("dob", value)}}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"/>


                </div>
                <div className="col-md-3">
                  <span>Gender</span><font className="text-danger">   *  </font>
                  <select id="gender" value={this.props.data.gender}  onChange={this.handleChange} className="form-control" type="text" > 
                  {this.getSelectOptions("gender")}
                  </select>
                </div>
               
                <div className="col-md-3">
                <span>Email ID</span>
                  { (this.props.id === "owner_person" || this.props.id === "contact_person")  ?
                   <div></div>
                  :<font className="text-danger">   *  </font>
                  }
                  <input id="email_id" value={this.props.data.email_id}  className="form-control"  onChange={this.handleChange} type="text" required="required"/>
                </div>
                            
            <div className="col-md-3">
              <span>Mobile Number</span><font className="text-danger">   *  </font>
            <input id="mobile_num" value={this.props.data.mobile_num}  onChange={this.handleChange} onKeyUp={this.handleMakeCopy("whatsapp")} className="form-control" type="text"/>
            </div>
          </div>
            <br />
            <div className="row">
           <div className="col-md-3">
              <span>Messenger Number</span>
              <input id="whatsapp" value={this.props.data.whatsapp}  onChange={this.handleChange}  className="form-control" type="text"/>
            </div> 
                <div className="col-md-3">
                  <span>Landline</span>
                  <input id="phone_num" value={this.props.data.phone_num}  onChange={this.handleChange} className="form-control" type="text"  />
                </div>


                  <div className="col-md-3">
                <span>Designation</span>
                  { (this.props.id === "owner_person" || this.props.id === "contact_person")  ?
                   <div></div>
                  :<font className="text-danger">   *  </font>
                  }
                      <input id="designation" value={this.props.data.designation}  onChange={this.handleChange} className="form-control" type="text" required="required"/>
                </div>

                <div className="col-md-3">
                  <span>National ID</span><font className="text-danger">   *  </font>
                  <input type="text" id="national_id" value={this.props.data.national_id} onChange={this.handleChange} className="form-control" required="required"/>
                </div>
            </div>
            <br/>
           
            <div className="row">
              <div className="col-md-3">
                  <span>Nationality</span>
                  <input id="nationality" value={this.props.data.nationality}  onChange={this.handleChange} className="form-control" type="text"/>
              </div>
             </div>
             <br/>
              {this.props.title == "Business Owner Details" &&
                <div>
                
                  <div className="row">

                      <div className="col-md-6">  
                        <FileUpload id="photo_national_id" label="National ID Photo" type="image" mode={this.props.mode} 
                        person_id={this.props.data.id} 
                        db_file_name={this.props.data.photo_national_id} 
                        onEditUpload={(e) => {this.handleChangeForEdit(e); this.handleSave(e, "reload")}} onCreateUpload={this.handleChange}/>
                      </div>
                  </div>
                  <br/>
                  <div className="row">
                    <div className="col-md-6">
                     
                      <FileUpload id="photo_pps" label="Passport Size Photo" person_id={this.props.data.id} 
                      db_file_name={this.props.data.photo_pps} type="image" 
                      mode={this.props.mode} onEditUpload={(e) => {this.handleChangeForEdit(e); this.handleSave(e, "reload")}} onCreateUpload={this.handleChange}/>

                    </div>
                    
                  </div>
                   <br/>
                   <p  style={{color: "red"}}>Note : Ensure the photos in the National ID, Passport Photo matches with the person in Selfie</p>
                   <div className="row">
                    <div className="col-md-6">
                     
                      <FileUpload id="photo_selfie" label="Selfie with Customer" person_id={this.props.data.id} 
                      db_file_name={this.props.data.photo_selfie} type="image" 
                      mode={this.props.mode} onEditUpload={(e) => {this.handleChangeForEdit(e); this.handleSave(e, "reload")}} onCreateUpload={this.handleChange}/>

                    </div>
                    
                  </div>
              </div>
              
              }
             
           
            <br/>
          </div>
      </div>
  
    );
  }
}


export default Person;
