import React from 'react';
import FlowContainer from '../../common/container/core/FlowContainer';
import DateRange from '../../common/component/DateRange';
import '../../../styles/flow.css';
import {getReport} from '../../../actions/report_actions';
import {dd_value} from '../../../actions/common_actions';
import {rt_algn, lbl_amt, format_date} from '../../../helpers/common_helper';
import {get} from '../../../helpers/storage_helper';
import DatePicker from 'react-datetime';
import $ from 'jquery';


class CommonReportContainer extends FlowContainer {

state = {
  rows : null,
  headers: null,
  date_range: {}

}

get_header(header_arr){
  const report_header =  header_arr.map((header) => {
                return (
                          <th>{header}</th>
                      );
                      });
  report_header.unshift(<th>No</th>)
  return report_header;
}
  

  format_metric(value, unit){
  

  
   if(unit == "%") 
   {
      value = Number(value).toFixed(2) + " %"
   }
   else if (unit == "days")
   {
      value = Number(value).toFixed(0) + " days"
   }
   else if (unit == "UGX" ||unit == "USD")
   {
      return lbl_amt(Math.round(value), unit)
      //value = Number(value).toFixed(0)
   }
   else if(unit == "status") 
   {
      value = dd_value(value);
   }else if(unit == "date"){
      value = format_date(value);
   }
   
    return value;  

}


    get_rows(records_arr, units = "")
    { 
     
          const report_rows = records_arr.map((record,i)=>
            {
                const row = record.map((data, i) => <td>{this.format_metric(data, units[i])}</td>);
                return (
                    <tr key={i}>
                        <td>{i+1}</td>
                        {row}
                    </tr>
                );
          });
          
         return report_rows;
    }

  componentWillMount() {
    
    if(this.props.report_type != "daily_activity"){
      this.get_report();
    }
  }
  get_report(){
    const report_type = this.props.report_type
   const country_code = this.country_code
   //const data_prvdr_code = get('data_prvdr_code')
   const date_range = this.state.date_range;
   
    getReport(this.req({ report_type, country_code, date_range}, true))
          .then((response) => {
            if(!response){
              return };
              if(response.status === "success"){
                
                const records_arr = response.data.records;
                const units = response.data.units;
                
                const headers = this.get_header(response.data.header);
                const rows =  this.get_rows(records_arr, units);
              
                this.setState({headers, rows, title: response.data.title});

                $('#kpi_table').removeAttr('width').dataTable({
                    searching : true,
                    paging : true,
                    info : false,
                    dom:'Bfrtip',
                    buttons: [
                      {
                        title:"",
                        extend: 'print',
                        customize: function (win) {
                        
                        $(win.document.body)
                            .prepend(
                              '<img src="'+window.location.origin+'/logo_blue.png" alt = "FLOW" style="float:left;"/>'
                            );
                        }
                      },
                      {
                        extend:'csv',
                      }
                    ],
                    "order" : [],
                });


              
              }else{

                alert(response.message + "\n" + JSON.stringify(response.data));  
              }
            
          }
          );   
  }  

  handleSubmit = (event) => {
    this.get_report();
  }

render(){

      return (
      <div className="container">
        <h3>{this.state.title}</h3>

        {this.props.report_type == "daily_activity" && 
          <DateRange id="date_range" data={this.state.date_range} onSubmitClick={this.handleSubmit} 
                      onComponentChange={this.handleComponentChange} />
        }
          <div id='kpi_datatable' >
            <table id="kpi_table" className="display strip row-border order-column report flow-data-table flow-data-table-wide_header">
                    <thead>
                      <tr>
                        {this.state.headers}
                      </tr> 
                    </thead>
                    <tbody>
                        {this.state.rows}
                    </tbody>
            </table>
          </div>
      </div>
      );
  }
}

export default CommonReportContainer;
