import React from 'react';
import Route from 'react-router-dom/Route';

import BorrowerSearchContainer from './../UI/borrower/container/borrower_search_container';
import IndividualBorrowerContainer from './../UI/borrower/container/individual_bwr_container';
import InstitutionalBorrowerContainer from './../UI/borrower/container/institutional_bwr_container';
import ViewIndividualBorrowerContainer from './../UI/borrower/container/view_individual_bwr_container';
import ViewInstitutionalBorrowerContainer from './../UI/borrower/container/view_institutional_bwr_container';

import FlowRelationshipManagerContainer from './../UI/relationship_manager/container/flow_relationship_manager_container';
import DataprvdrRelationshipManagerContainer from './../UI/relationship_manager/container/dataprvdr_relationship_manager_container';
import ListDataprvdrRelationshipManagerContainer from './../UI/relationship_manager/container/list_dataprvdr_relationship_manager_container';
import listFlowRelationshipManagerContainer from './../UI/relationship_manager/container/list_flow_relationship_manager_container';

import ProductSelectionContainer from './../UI/loan_appl/container/product_selection_container';
import LoanApplSearchContainer from './../UI/loan_appl/container/loan_appl_search_container';
import ViewLoanApplContainer from './../UI/loan_appl/container/view_loan_appl_container';
import LoanSearchContainer from './../UI/loan/container/loan_search_container';
import ViewLoanContainer from './../UI/loan/container/view_loan_container';
import KYCProcess from './../UI/kyc/component/KYCProcess';
import UploadCustomerTransactionContainer from './../UI/borrower/container/upload_customer_transaction_container';
export const CustomerRoutes                 = (
    <>
                <Route path={`/borrower/indiv/create`} exact  component={() => <IndividualBorrowerContainer mode='create' title="Create Individual Customer"/>}/>
                <Route path={`/borrower/ins/create`} exact  component={() => <InstitutionalBorrowerContainer mode='create' title="Create Institutional Customer"/>}/>
                <Route path={`/borrower/indiv/edit/:cust_id`} exact  component={(props) => <IndividualBorrowerContainer mode='view' title="View/Edit Customer"  {...props}/>}/>
                <Route path={`/borrower/ins/edit/:cust_id`} exact  component={(props) => <InstitutionalBorrowerContainer mode='view' title="View/Edit Customer"  {...props}/>}/>  
                <Route path={`/borrower/indiv/view/:cust_id`} exact  component={(props) => <ViewIndividualBorrowerContainer mode='view' title="View Customer"  {...props}/>}/>
                <Route path={`/borrower/ins/view/:cust_id`} exact  component={(props) => <ViewInstitutionalBorrowerContainer mode='view' title="View Customer"  {...props}/>}/>             
                <Route path={`/borrower/search`} exact component={BorrowerSearchContainer}/>
                
                <Route path={`/upload_cust_txns`} exact component={() => <UploadCustomerTransactionContainer mode="create"/>}/>

                
   </>                
)

export const FLOWRelMgrRoutes                 = (
    <>               
                <Route path={`/relationship_manager/flowrlspmanager/create`} exact  component={() => <FlowRelationshipManagerContainer mode='create' title="Create FLOW Relationship Manager"/>}/>
                <Route path={`/relationship_manager/flowrlspmanager/view/:relationshipManager_id`} exact  component={(props) => <FlowRelationshipManagerContainer mode='view' title="View/Edit Relationship Manager" {...props}/>}/>
                <Route path={`/relationship_manager/flowrlspmanager/list`} exact component={listFlowRelationshipManagerContainer}/>
   </>                
)

export const DPRelMgrRoutes                 = (
    <>               
                <Route path={`/relationship_manager/dprlspmanager/create`} exact  component={() => <DataprvdrRelationshipManagerContainer mode='create' title="Create Data Provider Relationship Manager"/>}/>
                <Route path={`/relationship_manager/dprlspmanager/view/:relationshipManager_id`} exact  component={(props) => <DataprvdrRelationshipManagerContainer mode='view' title="View/Edit Relationship Manager" {...props}/>}/>
                <Route path={`/relationship_manager/dprlspmanager/list`} exact component={() => <ListDataprvdrRelationshipManagerContainer mode='list' />}/>
</>                
)

export const LoanApplRoutes                 = (
    <>
                <Route path={`/fa_appl/search`} exact component={LoanApplSearchContainer}/>
                <Route path={`/fa_appl/view/:loan_appl_doc_id`} exact component={(props) => <ViewLoanApplContainer mode="view" {...props}/>}/>         

                <Route path={`/fa_appl`} exact component={(props) => <ProductSelectionContainer mode="search" {...props}/>}/>
                <Route path={`/fa_appl_for/:cust_id`} component={(props) => <ProductSelectionContainer mode="result" {...props}/>}/>

                

     </>                
)

export const LoanRoutes                 = (
    <>
                <Route path={`/fa/view/:loan_doc_id`} exact component={(props) => <ViewLoanContainer {...props}/>}/>
                <Route path={`/fa/search`} exact component={LoanSearchContainer}/>

     </>      
)
export const KYCRoutes                 = (
    <>
                <Route path={`/kyc/:cust_id`} exact component={(props) => <KYCProcess {...props}/>}/>
    
     </>      
)


