import {call_fetch, admin_path} from './../helpers/fetch_helper';

export const createAgreement = request  => {
   return call_fetch(admin_path + '/agreement', request);
}

export const listAgreement = request  => {
   return call_fetch(admin_path + '/agreement/list', request);
}

export const generateAgreement = request  => {
   return call_fetch(admin_path + '/agreement/cust', request);
} 

export const getExistingAgreements = request  => {
   return call_fetch(admin_path + '/agreement/aggrs', request);
}

export const updateAggrStatus = request  => {
   return call_fetch(admin_path + '/agreement/status', request);
}

export const saveAgreement = request  => {
   return call_fetch(admin_path + '/agreement/save', request);
}

export const loadAgreementDetails = request  => {
   return call_fetch(admin_path + '/agreement/load', request);
}

export const listProducts = request  => {
   return call_fetch(admin_path + '/agreement/list_products', request);
}

