import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import { Modal, Button } from 'react-bootstrap';
import { set_logout } from '../../../helpers/storage_helper';

class DisplayImage extends FlowComponent {
	state = { 
		modalBox: false
	 };
	handleImage = (event) => {
		
		this.setState({modalBox: true});

	}
	handleClose = (event) => {
		this.setState({modalBox: false});
	}
	render(){
		console.log("this.root-"+this.root);
		console.log("this.props.image_path-"+this.props.image_path);
		console.log("this.props.file_name-"+this.props.file_name);
		
		return(
			<>
				<b>{this.props.title}</b><br/>
				<>
					<div style={{maxHeight:'100px'}}>
						{this.props.file_name ? 
							<img style={{maxHeight:'100px', pointerEvents : 'auto'}} src={this.root + this.props.image_path + "/l_" + this.props.file_name} 
										alt={"/l_" + this.props.file_name} onClick={(e) => this.handleImage(e)}/>
									
						: <>Not uploaded</>}
						{/* {this.props.file_name ? 
							<img src={this.root + this.props.image_path + "/l_" + this.props.file_name} 
										alt={"/l_" + this.props.file_name} onClick={(e) => this.handleImage} />
									
						: <>Not uploaded</>} */}
					</div>
				</>
				<Modal show={this.state.modalBox} onHide={this.handleClose} className="model_window">
					<Modal.Header closeButton show={this.state.modalBox} onHide={this.handleClose}>
						<Modal.Title>{this.props.title}</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<img src={this.root + this.props.image_path + "/l_" + this.props.file_name}
									alt={"/l_" + this.props.file_name} />
					</Modal.Body>
					<Modal.Footer>
						{/* <Button variant="secondary" onClick={this.handleClose}>Close</Button> */}
					</Modal.Footer>
          		</Modal>
			</>
			);
	}
}

export default DisplayImage;