import {call_fetch, admin_path} from './../helpers/fetch_helper';

export const listMarket = request => {
  return call_fetch(admin_path + '/market/list', request);
}

export const createMarket = request => {
  return call_fetch(admin_path + '/market',request)
}

export const viewMarket = request => {
 return call_fetch(admin_path + '/market/view',request)	
}

