import {  Redirect } from 'react-router-dom';
import React from 'react';
import {createLoanProductTemplate, viewLoanProductTemplate} from '../../../actions/loan_product_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import LoanProduct from '../component/LoanProduct';
import '../../../styles/flow.css';


class ProductTemplateContainer extends FlowContainer {

  state = {
    toList: false,    
    loan_product_template: {flow_fee_duration : 'entire_duration',flow_fee_type : 'Flat'},

  }

  componentWillMount() {    
    if(this.props.mode === 'view'){
      const product_template_id = this.props.match.params.product_template_id;
      var loan_product_template = null; 

      viewLoanProductTemplate(this.req({product_template_id}))
        .then((response) => {
          if(!response){return };
            loan_product_template = response.data;
            this.setState({loan_product_template: loan_product_template});            
          }
        );
    }
  } 

  handleSubmit = (event)=>{
    event.preventDefault();
    var loan_product_template = this.state.loan_product_template;
    loan_product_template.country_code = this.country_code;
    loan_product_template.status = "enabled";
    createLoanProductTemplate(this.req({loan_product_template}))
        .then((response) => {
          if(!response){return };
            if(response.status === "success"){
              this.setState({toList: true});
              alert("New FA Product Template created successfully");
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );
  }

 
  render(){
    if (this.state.toList === true) {
      return <Redirect to={`/fa_product/template/list`}/>
      //return <Redirect to={`/borrower/indiv/view/${this.state.new_loan_product_template_id}`}/> 
    }
    if(this.props.mode === 'view'){
      return  (
        <div className="container">
         <div id="">
        <LoanProduct id="loan_product_template" data={this.state.loan_product_template} 
                    mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}/>
          </div>
      </div>
     )
    }
      return (
      <div className="container">
         <h4>{this.props.title}</h4> 
        <form id="loan_product_template_form" onSubmit={this.handleSubmit}>
            <div id="">
              <LoanProduct id="loan_product_template" data={this.state.loan_product_template} 
                    mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}/>
              <br/>
            <input type="button" className={`btn btn-primary ${this.props.mode === "create" ? "show_button": "hide_button"}`} onClick={this.handleSubmit} value="Create Template" />
          </div>
        </form>  
        <br/><br/>
      </div>
      );
  }
}

export default ProductTemplateContainer;