import {  Redirect } from 'react-router-dom';
import React from 'react';
import {createBorrower, viewBorrower} from '../../../actions/borrower_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import Borrower from '../component/Borrower';
import AddressInfo from '../../common/component/AddressInfo';
import Person from '../../common/component/Person';
import Organisation from '../../common/component/Organisation';
import '../../../styles/flow.css';

class InstitutionalBorrowerContainer extends FlowContainer {

  state = {
    toList: false,
    org:{touch: null},
    cust_id: null,
    biz_address: {touch: null},
    reg_address: {touch: null},
    contact_person : {touch: null},
    borrower: {},
    address_div_style: "show_div",
    same_as_biz_address:false,
  }

  componentWillMount() {
    if(this.props.mode === 'view'){
      this.cust_id = this.props.match.params.cust_id;
      const request = {cust_id: this.cust_id, screen: "edit"};
      viewBorrower(this.req(request))
        .then((response) => {
          if(!response){return };
            this.borrower = response.data;
            this.setState({borrower: this.borrower,
                            org: this.borrower.org,
                            biz_address: this.borrower.biz_address,
                            reg_address: this.borrower.org.reg_address,
                            contact_person: this.borrower.contact_person,
                           });
          }
        );
    }else if(this.props.mode  === 'create'){
      
     /* var org = {};
      var reg_address = {};
      var head_person = {};
      org.name = "Uganda Pvt Ltd";
      borrower.data_prvdr_id =1 ;
      borrower.lender_id =1 ;
      reg_address.addr_type = "Residential";
      reg_address.door_num = "123";
      reg_address.addr1 = "Miru IT Park";
      reg_address.addr2 = "ADdr2";
      reg_address.province = "TN";
      reg_address.city = "NGL";
      reg_address.country_code = "IN";
      reg_address.landmark = "NA";

      head_person.first_name = "TEST";
      head_person.middle_name = "TEST";
      head_person.last_name = "TEST";
      head_person.initials= "TEST";
      head_person.dob= "1986-01-01";
      head_person.gender= "TEST";
      head_person.whatsapp= "TEST";
      head_person.email_id= "TEST@TEST.COM";
      head_person.mobile_num= "TEST";
      head_person.phone_num= "TEST";
      head_person.designation= "TEST";*/
  
  

      /*this.setState({
                        borrower,
                        org: org,
                        owner_address: reg_address,
                        contact_person: head_person,
                        biz_address: reg_address
                       });*/
    }
  } 

  handleSubmit = (event)=>{
    event.preventDefault();
    var borrower = this.state.borrower;
    borrower.country_code = this.country_code;
    borrower.biz_type = "Institution";
    borrower.biz_address = this.state.biz_address;
    borrower.biz_address.country_code = this.country_code;
    borrower.org = this.state.org;
    borrower.org.country_code = this.country_code;
    borrower.org.reg_address = this.state.reg_address;
    borrower.org.reg_address.country_code = this.country_code;
    borrower.contact_person = this.state.contact_person;
    borrower.contact_person.country_code = this.country_code;
    borrower.same_as_biz_address = this.state.same_as_biz_address;
    createBorrower(this.req({borrower}, true))
        .then((response) => {
          if(!response){return };
            if(response.status === "success"){
             
              this.setState({cust_id: response.data});
              this.setState({toList: true});
              alert("New Institutional Customer created successfully");
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );
  }

  handleCheckboxClick = (event) => {
    if(event.target.checked){
      this.setState({address_div_style: "hide_div",same_as_biz_address:true});
    }else{
      this.setState({address_div_style: "show_div",same_as_biz_address:false});
    }
  }

 
  render(){
    if (this.state.toList === true) {
      return <Redirect to={`/borrower/ins/edit/${this.state.cust_id}`}/> 
    }
      return (
 
      <div className="container">
         <h4>{this.props.title + (this.state.borrower.name? " : " + this.state.borrower.name : "") +" "+(this.cust_id? this.cust_id : "")}</h4> 
        <form id="borrower_form" onSubmit={this.handleSubmit}>
            <div id="">
            <Borrower id="borrower" title="Customer Details" data={this.state.borrower} 
                    mode={this.props.mode} screen={this.props.screen}
                    onComponentChange={this.handleComponentChange}/>
            <br/>
            <AddressInfo id="biz_address" screen="borrower" title="Business Address" data={this.state.biz_address} 
                    mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}
                    onComponentReset={this.handleComponentReset}/>
            <br/>
            <Organisation id="org" title="Organisation Details" component="Institutional Borrower" data={this.state.org} mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}/>
            
            <br/>
            { (this.props.mode === "view") ?
              <AddressInfo id="reg_address" title="Registered Address" data={this.state.reg_address} mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}
                    onComponentReset={this.handleComponentReset}/>
            :
            <div>
            <label className="checkbox-inline">
            <h4>Registered Address&nbsp;&nbsp;<input type="checkbox" onClick={this.handleCheckboxClick} />&nbsp;&nbsp;{"Same as Business Address"}</h4>
            </label>
            <div className={`${this.state.address_div_style}`} >
            <AddressInfo id="reg_address"  screen="borrower" data={this.state.reg_address} mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}
                    onComponentReset={this.handleComponentReset}/>
            </div>
            
            <hr/>
            </div>
            }
            <Person id="contact_person" title="Contact Person" val="Borrower Contact Person" data={this.state.contact_person} 
                    mode={this.props.mode} 
                    onComponentChange={this.handleComponentChange}/>
            <br/>
            
            <input type="button" className={`btn btn-primary ${this.props.mode === "create" ? "show_button": "hide_button"}`} onClick={this.handleSubmit} value="Create New Customer" />

          </div>

        </form>  
        <br/><br/>
      </div>

      );
  }
}

export default InstitutionalBorrowerContainer;