import React from 'react';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import {listAccount,updatePrimaryAccount} from '../../../actions/account_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import {dd_value} from '../../../actions/common_actions';
import {AccountTransaction} from '../component/AccountTransaction';
import StatusButton from '../../common/component/core/StatusButton';
import {lbl_amt} from '../../../helpers/common_helper';
import  {check_priv} from '../../../helpers/storage_helper';

import $ from 'jquery';
class ListAccount extends FlowContainer {

  state = {
     accountList: null  ,
     editScreen: false
  }


   updatePrimaryAccount = (event) => {
    const is_primary_acc = true;
      var request = {};
      if(this.props.entity === "customer"){
        const cust_id = this.props.entity_id;  
        request = {"id":event.target.id,cust_id,"is_primary_acc":is_primary_acc};
      }else if(this.props.entity === "lender"){
         const lender_code = this.props.entity_id;
         request = {"id":event.target.id,lender_code,"is_primary_acc":is_primary_acc};

      }
    const request_data = {"account":request}
      updatePrimaryAccount(this.req(request_data))
         .then((response) => {
          if(!response){return };
              if(response.status === "success"){
                window.location.reload();
              }else{
                alert(response.message + "\n" + JSON.stringify(response.data));  
              }
            }
          ); 

        }
  makePrimaryButton(account_id, is_primary_acc, status){
    if(is_primary_acc == 0 && status === "enabled"){
      return <input id={account_id} type='button' className='btn btn-primary' onClick={this.updatePrimaryAccount} value="Make Primary A/C " />
    }else{
      return null;
    }
  }

  handleEditAccount = (event) => {
    //this.setState({editScreen: true, account_id: event.target.id});
    this.props.onEditClick(event);

  }
  handleAccTxns = (event) => {
    const acc_id = event.target.id;
    this.entity_id = this.props.entity_id;    
    this.entity = this.props.entity;    
    window.open("/acc_txns/"+this.entity+"/"+this.entity_id+"/"+acc_id,"_blank");
  }

  componentWillMount(){
      const country_code = this.country_code;
      this.account = {}
      this.table_id = this.props.entity_id;  
      this.account = {country_code};
      if(this.props.entity == "customer"){
        this.account.cust_id = this.props.entity_id;  
        this.list_accounts(this.account);
      }else if(this.props.entity == "lender"){
        this.account.lender_code = this.props.entity_id;
        this.setOptionsToStateFromApi("data_provider");
      }else if(this.props.entity == "data_prvdr"){
         this.account.data_prvdr_code = this.props.entity_id;
         this.list_accounts(this.account);
      }

      if(check_priv(this.props.entity , 'view_acc_txn')){
          this.view_txn_btn_style = "show_div"
      }else{
          this.view_txn_btn_style = "disable_div"
      }



    
  }

  list_accounts(account){
      $('#table2').DataTable().destroy();
     
      listAccount(this.req({account}))
        .then((response) => {
          if(!response){return };
          const accountList = response.data.map((account,i)=>
            <tr key={i}>
            <td > {account.acc_prvdr_name}</td>
            <td > {dd_value(account.type, 'account_type')}</td>            
            <td > {account.acc_number}</td>
            <td > {dd_value(account.acc_purpose, 'account_purpose')}</td>          
            <td > {lbl_amt(account.balance, this.currency_code)}</td>           
            <StatusButton id={account.id} parent={this.props.entity} entity="accounts" status={account.status}/>
            {/*<td><input id={account.id} type="button" value="Edit" onClick={this.handleEditAccount} class="btn btn-primary"/></td>*/}
            <td><input id={account.id} type="button" value="View Txns" onClick={this.handleAccTxns} className={`btn btn-primary ${this.view_txn_btn_style}`}/></td>
            {this.props.entity == "lender" &&
              <td><AccountTransaction selected_data_prvdr_code={this.selected_data_prvdr_code} account={account} id="acc_txn" entity={this.props.entity} entity_id={this.props.entity_id} /></td>
            }
            </tr>
          );
      
          this.setState({accountList: accountList, 
            accounts: response.data,
            table_id: this.table_id});
             $('#table2').removeAttr('width').dataTable({
                "order" : [],
                    dom:'Bfrtip',
                    buttons: [
                    'csv','excel','print'
                    ]
                    });                 
        });
  }


  changeDataPrvdr = (event) => {
    var table = $('#table2').DataTable();
    table.destroy();
    this.selected_data_prvdr_code = event.target.value;
    this.account.lender_data_prvdr_code = event.target.value;
    this.list_accounts(this.account);
  }

  render(){
    return( 
        <>
          {this.props.entity == "lender" &&
          <div className='row'>
          <div className="col-md-3">
                <span>Data Provider</span><font className="text-danger">   *  </font>
                <select id="lender_data_prvdr_code" onChange={this.changeDataPrvdr}  className="form-control" type="text" required="required">
                   {this.state.data_provider_dd}
                </select>
          </div>
          </div>
        }
       
                {this.state.accountList != null &&
                    <div id='datatable1'>
                           <table id ='table2' className="display strip row-border order-column list">
                                <thead>
                                <tr><th>Account Provider Name</th>
                                <th>Type</th> 
                                <th>Account Number</th>
                                <th>Account Purpose</th>
                                <th>Balance</th>  
                                <th>Status</th>
                                {/*<th>Action</th>*/}
                                <th>Action</th>
                                <th>Action</th>
                                {this.props.entity == "lender" &&
                                <th>Action</th>
                                }
                                </tr></thead>
                                <tbody> 
                                {this.state.accountList}
                                </tbody>
                           </table>          
                      </div>
                }  

                {this.state.accountList == null &&  
                  <div><p>No Accounts Available ! </p></div>
                }
      
      </>
      );      
 } 
 
}


export default ListAccount;
