import {call_fetch, admin_path} from './../helpers/fetch_helper';
export const getReport = request => {
   return call_fetch(admin_path + '/report', request);
}

export const getKPIReport = request => {
   return call_fetch(admin_path + '/report/flow_kpi_report', request);
}

export const getCustPerformance = request => {
   return call_fetch(admin_path + '/report/growth_chart', request);
}


