import React from 'react';
import { Redirect } from 'react-router-dom';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import {listLoanProduct} from '../../../actions/loan_product_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import  {lbl_dur, lbl_flow_fee_obj, lbl_flow_fee_duration} from '../../../helpers/product_helper';
import StatusButton from '../../common/component/core/StatusButton';
import EditProduct from '../component/EditProduct';
import {rt_algn,lbl_amt} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';
import {check_priv} from '../../../helpers/storage_helper';
import $ from 'jquery';

class listProductContainer extends FlowContainer {
  state = {
     product_list: null,
     toView: false,
     toEdit: false,
     product_list_div_style:"hide_div",
     product_id: null
  }

  handleClick = (event) => {
       
        this.setState({product_id: event.target.id, toView: true});
    }

    handleEditClick = (event, product_details) => {
      this.setState({product_details, toEdit: true});
    }
 
  componentWillMount(){

    const can_edit = check_priv('product', 'edit');
    //const data_prvdr_code = this.data_prvdr_code;
    listLoanProduct(this.req(null, true))
        .then((response) => {
          if(!response){return };
              const product_list = response.data.map((product,i)=>
              <tr key={i}>
                <td> {product.product_name}</td>  
                <td> {product.product_code}</td>
                
                <td> {product.lender}</td>
                <td> {product.product_type}</td>
                <td data-sort={product.cs_model_code}> {dd_value(product.cs_model_code)}</td>
                <td data-sort={product.max_loan_amount}> {rt_algn(lbl_amt(product.max_loan_amount, this.currency_code))}</td>
                <td> {lbl_dur(product.duration, product.flow_fee_duration) } </td>
                {/*<td> {product.flow_fee_type}</td>*/}
                <td data-sort={product.flow_fee}> {lbl_flow_fee_obj(product, this.currency_code)} </td>
                {/*<td> {lbl_flow_fee_duration(product.flow_fee_duration, product.duration)}</td>*/}
                <td data-sort={product.penalty_amount}> {rt_algn(product.penalty_amount)}</td>
                <StatusButton id={product.id} entity="loan_products" status={product.status}/>


                {can_edit &&
                  <td key={i}> <input type="button" id={product.id} product={product} onClick={(e) => this.handleEditClick(e, product)} value="Edit" className="btn btn-primary" /></td>
                }
              </tr>
        );
          this.setState({product_list: product_list, can_edit});
              var col_width = [


                       {"width" : "10%"}, 
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "10%"},
                       {"width": "5%"},
                       {"width": "10%"},
                       {"width": "15%"},
                       {"width": "15%"},
                       {"width": "10%"},
                       
                      
                              


                     ];

              if(can_edit){
                 col_width.push({"width": "10%"});
              }

              $('#list_product').removeAttr('width').dataTable({
                    "order" : [],
                    "columns": col_width,
                    "columnDefs":[
                    { 
                      "type":"num-fmt","targets":[4,7,9]}
                    ],


                  dom:'Bfrtip',
                  buttons: [
                  'csv','excel','print'
                  ]
              });

        });
  }


  render(){
      
     if (this.state.toView === true) { 
           return <Redirect to={`/fa_product/view/${this.state.product_id}`} />
    }
     else if (this.state.toEdit === true) { 
           return <EditProduct id="product" product={this.state.product_details} onComponentChange={this.handleComponentChange} />
     }

   if (this.state.product_list != null) { 
      
    return( 
          <div className="">
            <div id="" className="">
                <div id='datatable'>
                 <h4>List of FA Products</h4>
                    <table id ='list_product' className="display strip row-border order-column list">
                        <thead>
                        <tr>
                        <th>Product Name</th>    
                        <th>Product Code</th> 
                        
                        <th>Lender</th>
                        <th>Product Type</th>
                        <th>Credit Score Model</th>
                        <th>FA Amount</th>
                        <th>FA Duration</th>
                       
                        <th>Flow Fee</th>
                       
                        <th>Penalty Amount</th>
                        <th>Status</th>
                        <th>Action</th>
                        {this.state.can_edit &&
                          <th>Action</th>
                        }
                        </tr></thead>
                        <tbody> 
                          {this.state.product_list}
                        </tbody>
                    </table>                    
                </div>
            </div>
          </div>
      );
    }
      return (
        <div><p>No FA Products Available ! </p></div>
      );
  }
}
export default listProductContainer;
